import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { photo } from './../../../config';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import axios from 'axios'
@Injectable()

export class photoService {
private listURL: string = photo.list;
private tagListURL: string = photo.tagList;
private addTagURL: string = photo.addTag;
private deillegalURL: string = photo.deillegal;
private illegalURL: string = photo.illegal;
private pushTagURL: string = photo.pushTag;
    constructor(private http: Http) { }
setDeillegal(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.put(this.deillegalURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
        setIllegal(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.put(this.illegalURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
getList(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.listURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
     getTagList(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.tagListURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    addTag(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.addTagURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    renameTag(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.put(this.addTagURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
     pushTag(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.put(this.pushTagURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    deleteTags(data): Promise<any> {
        return new Promise((resolve, rej) => {
            axios({
                method: 'delete',
                url: this.addTagURL,
                data: data,
                headers: {
                    "Content-Type": "application/json",
                    'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token
                },
            })
                .then((res) => {
                    if (res.status == 200) {
                        resolve(res.data.data)
                    }
                })
        })
    }
   
    private extractData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }
}


