import { Component,Input,Output,EventEmitter } from '@angular/core';
import _ from "underscore" 

@Component({
  selector: 'app-tagEdit',
  templateUrl: './tagEdit.component.html',
  styleUrls: ['./tagEdit.component.less']
})
export class TagEditComponent {
@Input() title;
@Input() tags;
newTag="";
addNewTag=false;
selectTags=[];
renameList=[]
@Output() tagsClo = new EventEmitter<any>();
@Output() tagsRemaneSub = new EventEmitter<any>();
@Output() tagsDeleteList = new EventEmitter<any>();
@Output() tagsAddList = new EventEmitter<any>();
handleAddTag(e){
this.addNewTag=true;
}
handleChange(e,data){
  var even = _.find(this.selectTags, function(num){
      return num==data.uuid;
     });
    if(even||even==0){
      this.selectTags=_.without(this.selectTags, data.uuid);
    }else{
      this.selectTags.push(data.uuid)
    }
}
tagCancel(){
  this.renameList=[];
}
handleCancleAdd(){
  this.newTag="";
  this.addNewTag=false;
}
selectTag(data){
  var even = _.find(this.selectTags, function(num){
      return num==data.uuid;
     });
     if(even==0){
       return true;
     }
	 return even;
}
remaneTag(data){
  var even = _.find(this.renameList, function(num){
      return num==data.uuid;
     });
     if(even==0){
       return true;
     }
	 return even;
}
rename(){
  if(this.selectTags.length==1){
    this.renameList=this.selectTags.slice(0);
  }
  
}
delete(){
  this.tagsDeleteList.emit(this.selectTags)
}
tagSub(add){
  if(add){
    var newTagObj=[{
      "name":this.newTag      
    }]
  this.handleCancleAdd();
  this.tagsAddList.emit(newTagObj)
}else{
  var renameTag=[];
  this.tags.map((item,i)=>{
    if(item.uuid==this.renameList[0]){
renameTag.push(item);
    }
  })
  this.tagCancel();
   this.tagsRemaneSub.emit(renameTag)
  }
  
}
}






