import { Component,Input,Output,EventEmitter } from '@angular/core';

@Component({
  selector: 'app-warning',
  templateUrl: './warning.component.html',
  styleUrls: ['./warning.component.less']
})
export class WarningComponent {
@Input() btnFirst;
@Input() btnCancel;
@Input() btnFirstStyle;
@Input() btnCancelStyle;
@Output() warningWindowSub = new EventEmitter<any>();
@Output() warningWindowClo = new EventEmitter<any>();
ngOnChanges(){
}
}



