import { Component, ViewEncapsulation } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser'
import { DaterangepickerConfig } from 'ng2-daterangepicker';
import { userService } from "./user.service"
import { Router } from '@angular/router';
var moment = require('moment');
@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class UserComponent {
  constructor(private router: Router, private daterangepickerOptions: DaterangepickerConfig, private _sanitizer: DomSanitizer, private userService: userService) {

    this.daterangepickerOptions.settings = {
      locale: {
        format: 'YYYY-MM-DD',
        applyLabel: "应用",
        cancelLabel: "取消",
        autoUpdateInput: false,
        "daysOfWeek": [
          "日",
          "一",
          "二",
          "三",
          "四",
          "五",
          "六"
        ],
      },
      alwaysShowCalendars: false,
    };
  }
  statusValue: any = {
    name: ""
  };
  id=0;
  userTag = [];//用户拥有的标签
  userSelect = "";//选择的用户
  selectTag = false;//是否显示用户的打标签窗口
  dataTagSource = [];
  tagCurrentPage = 1;
  tagPageSize = 200;
  startTime = "";
  endTime = "";
  status = "";
  window = false;
  loading = false;
  userBy = "";
  currentPage = 1;
  pageSize = 100;
  dataSource = [];
  totalItems = 0;
  tip = false;
  checkArray = [];
  select = [];
  selectTags = [];
  tagsEdit = false;
  user_property_type = "";
  totalTagItems = 0;
  config = {
    select: false,//是否需要单选框
    headStyle: 'fixed',//是否固定顶部
    serial: true,//是否有序号
    currentPage: this.currentPage,
    pageSize: this.pageSize
  }
  alert = {
    message: "",
    type: "success"
  }
  columns = [{
    title: 'ID',
    dataIndex: 'id',
    width: 50
  }, {
    title: '用户昵称',
    dataIndex: 'nickname',
    width: 100,
    render: (text) => {
      return this.getDom(`
       <span class="${text.user_property_type}">${text.nickname}</span>
	  			`);

    }
  }, {
    title: '注册时间',
    dataIndex: 'user_create_at',
    width: 100,
    render: (text) => {
      return this.getDom(`
       <span>${moment(text).format("YYYY/MM/DD HH:mm:ss")}</span>
	  			`);
    }
  }, {
    title: '性别',
    dataIndex: 'gender',
    width: 100,
    render: (text) => {
      if (text == "user_gender_male") {
        return this.getDom(`
       <span>男</span>
	  			`);
      } else if (text == "user_gender_female") {
        return this.getDom(`
       <span>女</span>
	  			`);
      }
      return this.getDom(`
       <span>未知</span>
	  			`);
    }
  }, {
    title: '所属区域',
    dataIndex: 'location',
    width: 50
  }, {
    title: '拍照天数',
    dataIndex: 'photo_day_count',
    width: 150,
  }, {
    title: '注册天数',
    dataIndex: 'regist_day_count',
    width: 50,
  }, {
    title: '使用频率',
    dataIndex: 'useFrequency',
    width: 50,
  }, {
    title: '离上次使用天数',
    dataIndex: 'unused_length',
    width: 50,
    render: (text) => {

      return this.getDom(`
       <span>${this.formatDuring(text)}</span>
	  			`);
    }
  }, {
    title: '总拍照数',
    dataIndex: 'use_tuso_take_photo_count',
    width: 100,
  }, {
    title: '总上传数',
    dataIndex: 'uploadCount',
    width: 50,
  }, {
    title: '照片总数',
    dataIndex: 'images',
    width: 50,
  }, {
    title: '用户标签',
    dataIndex: 'tags',
    width: 100,
    render: (text) => {
      
      let resule=""
      if(text.length==0){
        resule="暂无标签";
      }
      text.map((item,i)=>{
        if(i==0){
        resule+=item.name;
        }else{
          resule+="、"+item.name;
        }
})
       return this.getDom(`
       <span>${resule}</span>
	  			`);

      
    }
  }, {
    title: '操作',
    dataIndex: 'nickname',
    width: 100,
    render: (text) => {
      if(text.user_property_type=="UserPropertyOwner"){
return this.getDom(`
		  			<div><a data-event=custom5 style="color: #3D95D1;text-decoration: underline;cursor: pointer;">查看照片</a></div>
		  			<div><a data-event=custom1 style="color: #3D95D1;text-decoration: underline;cursor: pointer;">编辑标签</a></div>
            <div><a data-event=custom4 style="color: #3D95D1;text-decoration: underline;cursor: pointer;">取消内部用户</a></div>
					  <div><a data-event=custom2 style="color: #E94646;text-decoration: underline;cursor: pointer;">加入黑名单</a></div>
	  		
	  			`);
      }else{
return this.getDom(`
		  			<div><a data-event=custom5 style="color: #3D95D1;text-decoration: underline;cursor: pointer;">查看照片</a></div>
		  			<div><a data-event=custom1 style="color: #3D95D1;text-decoration: underline;cursor: pointer;">编辑标签</a></div>
            <div><a data-event=custom3 style="color: #3D95D1;text-decoration: underline;cursor: pointer;">设为内部用户</a></div>
					  <div><a data-event=custom2 style="color: #E94646;text-decoration: underline;cursor: pointer;">加入黑名单</a></div>
	  		
	  			`);
      }
      
    }
  }]
  orderStatus = [{
    "id": "1",
    "status": "",
    "name": "所有用户",
    "color": "#A1A1A1"
  }, {
    "id": "2",
    "status": "UserPropertyNEWNoPhotos",
    "name": "新用户 未拍照",
    "color": "#4791D3"
  }, {
    "id": "3",
    "status": "UserPropertyNEWHasPhotos",
    "name": "新用户 已拍照",
    "color": "#64B53C"
  }, {
    "id": "4",
    "status": "UserPropertyOld",
    "name": "老用户",
    "color": "#F28D26"
  }, {
    "id": "5",
    "status": "UserPropertyOwner",
    "name": "内部用户",
    "color": "#C958CA"
  }, {
    "id": "6",
    "status": "UserPropertySilent",
    "name": "静默用户",
    "color": "#333333"
  }]
  getDom(str) {
    return this._sanitizer.bypassSecurityTrustHtml(str)
  }
  formatDuring(second_time) {

    var time = ""
    var day = 0;
    var hour = 0;
    var min = 0;
    if (second_time > 86400) {
      day = parseInt((second_time / 86400) + "")
    }
    second_time = second_time - day * 86400;
    if (second_time > 3600) {
      hour = parseInt((second_time / 3600) + "")
    }
    second_time = second_time - hour * 3600;
    if (second_time > 60) {
      min = parseInt((second_time / 60) + "")
    }
    second_time = second_time - min * 60;
    time = day + "天" + hour + "小时" + min + "分钟" + second_time + "秒"
    return time;
  }
  changeUserStatus(data) {
    this.status = data.status;
    this.statusValue = data;
    this.user_property_type = data.status;
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
  }
  ngOnInit() {
    if (localStorage.getItem("user") && JSON.parse(localStorage.getItem("user")).uuid) {
    } else {
      this.router.navigate(['/login']);
    }
    this.id=parseInt(localStorage.getItem("userUuid"));
    localStorage.removeItem("userUuid"); 
    this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
  }
  selectedDate(value: any) {
    this.startTime = moment(value.start._d).format();
    this.endTime = moment(value.end._d).format();
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
  }
  reset() {
    this.startTime = "";
    this.endTime = "";
    this.currentPage = 1;
    this.userBy = "";
    this.user_property_type="";
    this.id=0;
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
  }
  serchUserBy() {
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
  }
  handleModify(data) {

  }
  handleDelete(data) {

  }
  keywordSearch(e, type) {
    let keyCode = e.keyCode;
    if (type && keyCode == 13) {
      this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
    }
    if (!type) {
      this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
    }
  }
  onSelect(data) {
    this.select = data;
  }
  export() {
    this.window = true;
  }
  closeWindow() {
    this.window = false;
  }
  onPageChange(data) {
    if (!data.currentPage) {
      data.currentPage = 1;
    }
    this.config.currentPage = data.currentPage;
    this.currentPage = data.currentPage;
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
  }
  lookUser(data) {
    this.tagsEdit = false;
  }
  filtrateSub(data) {
    this.selectTags = data;
    // console.log(this.selectTags)
        this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
  }
  filtrateClo(data) {
    this.selectTags = data;
  }
  editLayerShow() {
    this.tagsEdit = true;
    this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
  }
  tagsClo() {
    this.tagsEdit = false;
  }
  tagsRemaneSub(data) {
    this.loading = true;
    this.userService.renameTag(data).subscribe(list => {
      if (list.data) {
        this.showAlert("修改成功", "success")
        this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
    // this.tagsEdit = false;
  }
  tagsDeleteList(data) {
    this.loading = true;
    this.userService.deleteTags(data).then(list => {
      if (list.success) {
        this.showAlert("删除", "success")
        this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    }).catch(function (err) {
      this.loading = false;
      this.showAlert("服务器异常，请联系工程部！", "error")
    });
  }
  tagsAddList(data) {
    this.loading = true;
    this.userService.addTag(data).subscribe(list => {
      if (list.data) {
        this.showAlert("新增成功", "success")
        this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.showAlert("服务器异常，请联系工程部！", "error")
        this.loading = false;
      });
  }
  showAlert(message: string, type: string) {
    this.alert = {
      message: message,
      type: type
    }
    this.tip = true;
    let that = this;
    setTimeout(function () {
      that.tip = false;
    }, 2000)
  }
  custom1(data) {
    this.userTag = data.tags
    this.userSelect = data.uuid
    this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
    this.selectTag = true;
  }
  userTagsClo() {
    this.selectTag = false;
  }
  pushTag(data) {
    let newData = []
    data.map((item, i) => {
      let temp: any = {};
      temp.target_uuid = this.userSelect;
      temp.tag_uuid = item;
      newData.push(temp);
    })
    this.loading = true;
    this.userService.pushTag(newData).subscribe(list => {
      this.loading = false;
      if (list.data && list.data.success) {
        this.showAlert("修改标签成功", "success");
        this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
        this.selectTag = false;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
        this.selectTag = false;
      }
    },
      error => {
        this.loading = false;
        this.selectTag = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });

  }
  custom2(data){
    this.loading = true;
    this.userService.close([data.id]).subscribe(list => {
      this.loading = false;
      if (list.data && list.data.success) {
        this.showAlert("加入黑名单成功", "success");
        this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
  }
  custom3(data){
  this.loading = true;
    this.userService.owner([data.id]).subscribe(list => {
      this.loading = false;
      if (list.data && list.data.success) {
        this.showAlert("加入内部用户成功", "success");
        this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
  }
  custom4(data){
     this.loading = true;
    this.userService.ownerCancel([data.id]).subscribe(list => {
      this.loading = false;
      if (list.data && list.data.success) {
        this.showAlert("移除内部用户成功", "success");
        this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy, this.user_property_type,this.id,this.selectTags)
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
  }
custom5(data){
  localStorage.setItem("userId", data.id);
    window.open(window.location.href.split('content/')[0] + "content/photo");
}

  getList(offsetHead, offsetTail, startTime, endTime, keywords, user_property_type,id,tags) {
    this.loading = true;
    let data:any = {
      "orderBy": "id",
      "IsDesc": true,
      "offsetHead": offsetHead,
      "offsetTail": offsetTail,
      "startTime": startTime,
      "endTime": endTime,
      "keywords": keywords,
      "tags":tags,
      "where": {     
        "user_property_type": user_property_type,
        "status":"USER_STATUS_ACTIVATED"
      }
    }
    if(id&&id!=0){
      data.where.id=id;
    }
    this.userService.getList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        this.loading = false;
        this.dataSource = list.data.list;
        this.dataSource.map((item, i) => {
          item.uploadCount = item.images - item.use_tuso_take_photo_count;
          item.useFrequency = (item.photo_day_count / item.regist_day_count).toFixed(2);
          if (item.regist_day_count == 0) {
            item.useFrequency = 0;
          }
          item.nickname = {
            nickname: item.nickname,
            user_property_type: item.user_property_type
          }
        })
        this.totalItems = list.data.count;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
  }
  getTagList(offsetHead, offsetTail) {
    this.loading = true;
    let data = {
      "orderBy": "current_date",
      "IsDesc": true,
      "offsetHead": offsetHead,
      "offsetTail": offsetTail
    }
    this.userService.getTagList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        this.dataTagSource = list.data.list;
        this.totalTagItems = list.data.count;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
  }
}



