import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { setCalendar } from './../../../config';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import axios from 'axios'
@Injectable()

export class setCalendarService {
    private getFilterListUrl = setCalendar.filterList;  // URL to web API
    private getMoodListUrl = setCalendar.moodList;
  
    private natures = setCalendar.naturesUrl;
    constructor(private http: Http) { }

    getFilterList(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.getFilterListUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    getMoodList(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.getMoodListUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    filterCover(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'multipart/form-data' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.getMoodListUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    deleteNatures(data): Promise<any> {
        return new Promise((resolve, rej) => {
            axios({
                method: 'delete',
                url: this.natures,
                data: data,
                headers: {
                    "Content-Type": "application/json",
                    'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token
                },
            })
                .then((res) => {
                    if (res.status == 200) {
                        resolve(res.data.data)
                    }
                })
        })
    }
    private extractData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }
}


