import {
  Component, Input,EventEmitter,Output
} from '@angular/core';
import { Router } from '@angular/router';
@Component({
  selector: 'app-createExcle',
  templateUrl: './createExcle.component.html',
  styleUrls: ['./createExcle.component.less']
})
export class createExcleComponent {
@Input() windowWidht;
@Input() url;
@Input() type;//0:开始生成，1：生成成功，2：生成失败
@Input() small;
@Output() closeWindow = new EventEmitter<any>();
@Output() smallWindow = new EventEmitter<any>();

}



