import { Component,Input,Output,EventEmitter } from '@angular/core';
import _ from "underscore" 

@Component({
  selector: 'app-tagTip',
  templateUrl: './tagTip.component.html',
  styleUrls: ['./tagTip.component.less']
})
export class TagTipComponent {
@Input() btnName;
@Input() tags;
@Input() selectTags;

@Output() filtrateSub = new EventEmitter<any>();
@Output() filtrateClo = new EventEmitter<any>();
tagsShow=false;
selectTagTemp=[];

selectTag(data){
  var even = _.find(this.selectTagTemp, function(num){
      return num==data.uuid;
     });
     if(even==0){
       return true;
     }
	 return even;
}

handleTagsShow(){
  if(!this.tagsShow){  
     this.selectTagTemp=this.selectTags.slice(0);
  }
  this.tagsShow=!this.tagsShow;
}
handleChange(e,data){
  var even = _.find(this.selectTagTemp, function(num){
      return num==data.uuid;
     });
    if(even||even==0){
      this.selectTagTemp=_.without(this.selectTagTemp, data.uuid);
    }else{
      this.selectTagTemp.push(data.uuid)
    }
}
filtrate(){
  this.filtrateSub.emit(this.selectTagTemp);
  this.tagsShow=false;
}
cancel(){
  this.selectTagTemp=this.selectTags;
  this.tagsShow=false;
 this.filtrateClo.emit(this.selectTags);
}

}






