import {
    Component, 
    Input,
    Output,
    EventEmitter
} from '@angular/core';
import _ from "underscore" 
@Component({
    selector: 'app-table',
    templateUrl: './table.component.html',
    styleUrls: ['./table.component.less']

})
export class TableComponent {
	selectedList = []
	totalItems=100;
	count:number=0;
	@Input() checkArray:any=[];
    @Input() config;
    @Input() columns;
    @Input() dataSource;
	
    @Output() onSelect = new EventEmitter();
    @Output() handleModify = new EventEmitter();
    @Output() handleDelete = new EventEmitter();
	@Output() lookUser = new EventEmitter();
    @Output() custom1 = new EventEmitter();
    @Output() custom2 = new EventEmitter();
    @Output() custom3 = new EventEmitter();
    @Output() custom4 = new EventEmitter();
    @Output() custom5 = new EventEmitter();
    @Output() custom6 = new EventEmitter();
    @Output() custom7 = new EventEmitter();
    @Output() custom8 = new EventEmitter();
    @Output() custom9 = new EventEmitter();
    @Output() custom10 = new EventEmitter();

	ngOnChanges(){
		this.count=(parseInt(this.config.currentPage)-1)*parseInt(this.config.pageSize);
	}
	pageChanged(){}
    handleClick(e,data){
		e.stopPropagation();
		// console.log(data)
		if(e.target&&e.target.getAttribute('data-event')){
       let event =e.target.getAttribute('data-event');
    	this[event].emit(data);
		
		}
    	
    }
    // handleChange(e,data){
    // 	if(e.target.checked){
    // 		if(checkArrRepeat(this.selectedList,data)){
    // 			this.selectedList.push(data)
    // 		}
    // 	}else{
    // 		let result = deleteArr(this.selectedList,data);
    // 		this.selectedList = result;
    // 	}
    // 	this.onSelect.emit(this.selectedList);
    // }
    // handleChangeAll(e){
    // 	this.selectedList = e.target.checked?this.dataSource:[];
    // 	this.onSelect.emit(this.selectedList);
    // }
hasCheck(data){
    if(!this.config.select){
    return false;
}
  var even = _.find(this.checkArray, function(num){
      return num==data.id;
     });
	 return even;
}
handleChange(e,data){
    if(!this.config.select){
    return false;
}
  var even = _.find(this.checkArray, function(num){
      return num==data.id;
     });
    if(even){
      this.checkArray=_.without(this.checkArray, data.id);
    }else{
      this.checkArray.push(data.id)
    }
	this.onSelect.emit(this.checkArray);
}
handleChangeAll(){
if(!this.config.select){
    return false;
}
 if(this.checkArray.length==0||this.checkArray.length<this.dataSource.length){
	 this.checkArray=[]
	this.dataSource.map((item,i)=>{
		this.checkArray.push(item.id);
	})
}else if(this.checkArray.length==this.dataSource.length){
this.checkArray=[]
}
this.onSelect.emit(this.checkArray);
}

}

function deleteArr(arr,obj){
	arr.map((v,k)=>{
		if(v.key == obj.key){
			arr.splice(k,1)
		}
	})
	return arr
}

function checkArrRepeat(arr,obj){
	let canPush = true;
	arr.map((v,k)=>{
		if(v.key == obj.key){
			canPush = false;
		}
	})
	return canPush
}


