import { Component } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser'
import { maJiaService } from "./majia.service"
import { photoService } from "./../photo/photo.service"
import { Router } from '@angular/router';
let _ = require("underscore")
var moment = require('moment');
declare var RGBaster: any;
declare var SparkMD5: any;
@Component({
  selector: 'app-majia',
  templateUrl: './majia.component.html',
  styleUrls: ['./majia.component.less']
})
export class MajiaComponent {
  currentPage: number = 1;
  totalItems: number = 2; // total numbar of page not items 
  pageSize: number = 10;
  loading = true;
  newNumber: number;
  errorMessage = ""
  pwd = "1"
  imagefile;
  alert = {
    message: "",

    type: "success"
  }

  upLoadIndex = 0;
  upLoadAll = [];
  userTemp;
  eve;
  user_uuid = "";
  dataSource = []
  tip = false;
  privewPic = ""
  picDataSource = [];
  videofile = "";
  picCurrentPage: number = 1;
  picTotalItems: number = 2; // total numbar of page not items 
  picPageSize: number = 10;
  liveLength=0;
  showWin = false;
  constructor(private _sanitizer: DomSanitizer,
    private maJiaService: maJiaService,
    private photoService: photoService,
    private router: Router) { }

  showAlert(message: string, type: string) {
    this.alert = {
      message: message,
      type: type
    }
    this.tip = true;

    let that = this;
    setTimeout(function () {
      that.tip = false;
    }, 2000)
  }
  createMaJia() {
    this.loading = true;
    let data = `count=${this.newNumber || 0}&pwd=${this.pwd}`
    this.maJiaService.addPup(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        // this.loading=false;
        this.currentPage = 1;
        this.showAlert("创建成功", "success")
        this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });
  }
  upLoadImg(data) {
    this.maJiaService.upload(data).subscribe(list => {
      this.imagefile = ""
      this.loading = false;
      if (list.success) {
        if (this.upLoadIndex + 1 < this.upLoadAll.length) {
          this.upLoadIndex += 1;
        } else {
          this.showAlert("上传成功", "success")
          this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
          this.upLoadAll = [];
          this.upLoadIndex = 0;
        }

      } else {
        this.imagefile = ""
        this.showAlert("上传失败", "error")
      }
    },
      error => {
        this.imagefile = ""
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });
  }
  upLoadVideo(videoFile) {
    this.maJiaService.upload(videoFile).subscribe(list => {
      if (list.success) {        
         this.liveLength-=1;
      if(this.liveLength==0){
        this.loading = false;
        this.videofile = ""
        this.showAlert("上传livephoto成功", "success")
        this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
      }else{
        this.showAlert("剩余"+this.liveLength+"个视频文件正在上传", "success")
      }
        
      } else {
        this.videofile = ""
        this.showAlert("上传失败", "error")
      }
    },
      error => {
        this.videofile = ""
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });
  }
  upLoadVideoImg(imgFile, videoFile, liveToken, author_token) {
    this.showAlert("正在上传图片", "warning")
    this.maJiaService.upload(imgFile).subscribe(list => {
      if (list.success) {
        var formData = new FormData();
        formData.append("file", videoFile);
        formData.append("x:ut", author_token);
        formData.append("token", liveToken);
        formData.append("frame_rate", "48");
        formData.append("x:photo_uuid", list.data.uuid + "");
        this.upLoadVideo(formData);
        this.showAlert("上传图片成功", "success")
      } else {
        this.showAlert("上传失败", "error")
      }
    },
      error => {
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });
  }
  changeCover(e, data) {
    this.upLoadAll = _.clone(e.target.files);
    let token = "";
    this.loading = true;
    this.maJiaService.getToken(data).subscribe(list => {
      if (list["QN-Token"]) {
        token = list["QN-Token"]

        for (var i = 0; i < e.target.files.length; i++) {
          this.uploadList(e, data, e.target.files, i, token)
        }
      }
      if (list.errors) {
        this.loading = false;
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });


  }
  changeVideo(e, data) {
    this.upLoadAll = _.clone(e.target.files);
    let token = "";
    let liveToken = "";
    this.loading = true;
    if (e.target.files.length % 2 == 1) {
      this.showAlert("上传数量必须为偶数", "error");
      this.videofile = "";
      this.loading = false;
      return false;
    }

    let files = [];
    let imgFiles = [];
    let videoFiles = [];
    for (var i = 0; i < e.target.files.length; i++) {
      if (new RegExp("image").test(e.target.files[i].type)) {
        imgFiles.push(e.target.files[i]);
      } else if (new RegExp("video").test(e.target.files[i].type)) {
        videoFiles.push(e.target.files[i]);
      } else {
        this.showAlert("第" + i + "个文件必须是图片或者视频，请重新选择", "error");
        this.videofile = "";
        return false;
      }
    }
    if (imgFiles.length != videoFiles.length) {
      this.showAlert("请保证图片和视频数量一样", "error");
      this.videofile = "";
      return false;
    }
    imgFiles.map((item, i) => {
      let fileObj;
      var even = _.find(videoFiles, function (num) {
        if (item.name.split(".")[0] == num.name.split(".")[0]) {
          fileObj = num
        }
        return item.name.split(".")[0] == num.name.split(".")[0];
      });
      if (even) {
        var obj = {
          "img": item,
          "file": fileObj
        }
        files.push(obj)
      } else {
        this.showAlert("匹配不到名为" + item.name + "的视频文件", "error");
        this.videofile = "";
        return false;
      }
    })
    this.liveLength=files.length;
    this.maJiaService.getToken(data).subscribe(list => {
      if (list["QN-Token"]) {
        token = list["QN-Token"]
        if (liveToken != "") {
          files.map((item, i) => {
            this.showAlert("正在整理视频和图片", "warning")
            this.upLoadVideoCover(e, data, item, token, liveToken)
          })
        }
      }
      if (list.errors) {
        this.loading = false;
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });
    this.maJiaService.getLivephotoToken(data).subscribe(list => {
      if (list["QN-Token"]) {
        liveToken = list["QN-Token"]
        if (token != "") {
          files.map((item, i) => {
            this.showAlert("正在整理视频和图片", "warning")
            this.upLoadVideoCover(e, data, item, token, liveToken)
          })
        }


      }
      if (list.errors) {
        this.loading = false;
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });

  }
  upLoadVideoCover(e, data, files, token, liveToken) {
    this.userTemp = data;
    this.eve = e;
    let primary_color = "";
    let md5 = "";
    let imageWidht = 0;
    let imageHeight = 0;
    var windowURL = window.URL;
    var file = files.img;
    var image = new Image();
    let that = this;
    var formData = new FormData();
    formData.append("file", file);
    formData.append("x:privacy", "photo_privacy_public");
    formData.append("x:geolocation", "{}");
    formData.append("x:edit_params", "{}");
    formData.append("x:timestamp", moment(new Date()).format());
    formData.append("x:ut", data.author_token);
    formData.append("x:exif", "{}");
    formData.append("x:ut", "{}");
    formData.append("token", token);
    image.src = window.URL.createObjectURL(files.img);
    image.onload = function () {
      imageWidht = image.width;
      imageHeight = image.height;
      formData.append("x:width", image.width + "");
      formData.append("x:height", image.height + "");
       if (primary_color != "" && md5 != "" && token != "") {
        that.upLoadVideoImg(formData, files.file, liveToken, data.author_token);
      }
    }

    RGBaster.colors(window.URL.createObjectURL(files.img), {
      success: function (payload) {
        var rgb = payload.dominant.split(',');
        var r = parseInt(rgb[0].split('(')[1]);
        var g = parseInt(rgb[1]);
        var b = parseInt(rgb[2].split(')')[0]);
        var hex = "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
        primary_color = hex
        formData.append("x:primary_color", hex);
         if (token != "" && md5 != "" && imageWidht != 0 && imageHeight != 0) {
          that.upLoadVideoImg(formData, files.file, liveToken, data.author_token);
        }
      }
    });
    var blobSlice = File.prototype.slice,
      chunkSize = 2097152,                             // Read in chunks of 2MB
      chunks = Math.ceil(file.size / chunkSize),
      currentChunk = 0,
      spark = new SparkMD5.ArrayBuffer(),
      fileReader = new FileReader();
    interface FileReaderEventTarget extends EventTarget {
      result: string
    }

    interface FileReaderEvent extends Event {
      target: FileReaderEventTarget;
      getMessage(): string;
    }
    let that1 = this;
    fileReader.onload = function (fre: FileReaderEvent) {
      spark.append(fre.target.result);                   // Append array buffer
      currentChunk++;
      if (currentChunk < chunks) {
        loadNext();
      } else {
        md5 = spark.end();
        formData.append("x:md5", md5);
        console.log(primary_color, md5, imageWidht, imageHeight)
        if (token != "" && md5 != "" && imageWidht != 0 && imageHeight != 0) {
          that1.upLoadVideoImg(formData, files.file, liveToken, data.author_token);
        }
      }
    };
    fileReader.onerror = function () {
      console.warn('oops, something went wrong.');
    };

    function loadNext() {
      var start = currentChunk * chunkSize,
        end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;
      fileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
    }
    loadNext();
  }
  uploadList(e, data, files, index, token) {
    this.userTemp = data;
    this.eve = e;
    let primary_color = "";
    let md5 = "";
    let imageWidht = 0;
    let imageHeight = 0;
    var windowURL = window.URL;
    var file = files[index];
    var image = new Image();
    let that = this;
    var formData = new FormData();
    formData.append("file", file);
    formData.append("x:privacy", "photo_privacy_public");
    formData.append("x:geolocation", "{}");
    formData.append("x:edit_params", "{}");
    formData.append("x:timestamp", moment(new Date()).format());
    formData.append("x:ut", data.author_token);
    formData.append("x:exif", "{}");
    formData.append("x:ut", "{}");
    formData.append("token", token);
    image.src = window.URL.createObjectURL(files[index]);
    image.onload = function () {
      imageWidht = image.width;
      imageHeight = image.height;
      formData.append("x:width", image.width + "");
      formData.append("x:height", image.height + "");
      if (primary_color != "" && md5 != "" && token != "") {
        that.upLoadImg(formData);
      }
    }

    RGBaster.colors(window.URL.createObjectURL(files[index]), {
      success: function (payload) {
        var rgb = payload.dominant.split(',');
        var r = parseInt(rgb[0].split('(')[1]);
        var g = parseInt(rgb[1]);
        var b = parseInt(rgb[2].split(')')[0]);
        var hex = "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
        primary_color = hex
        formData.append("x:primary_color", hex);
        if (token != "" && md5 != "" && imageWidht != 0 && imageHeight != 0) {
          that.upLoadImg(formData);
        }
      }
    });
    var blobSlice = File.prototype.slice,
      chunkSize = 2097152,                             // Read in chunks of 2MB
      chunks = Math.ceil(file.size / chunkSize),
      currentChunk = 0,
      spark = new SparkMD5.ArrayBuffer(),
      fileReader = new FileReader();
    interface FileReaderEventTarget extends EventTarget {
      result: string
    }

    interface FileReaderEvent extends Event {
      target: FileReaderEventTarget;
      getMessage(): string;
    }
    let that1 = this;
    fileReader.onload = function (fre: FileReaderEvent) {
      spark.append(fre.target.result);                   // Append array buffer
      currentChunk++;
      if (currentChunk < chunks) {
        loadNext();
      } else {
        md5 = spark.end();
        formData.append("x:md5", md5);
        if (token != "" && md5 != "" && imageWidht != 0 && imageHeight != 0) {
          that1.upLoadImg(formData);
        }
      }
    };
    fileReader.onerror = function () {
      console.warn('oops, something went wrong.');
    };

    function loadNext() {
      var start = currentChunk * chunkSize,
        end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;
      fileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
    }
    loadNext();
  }
  ngOnInit() {
    if (localStorage.getItem("user") && JSON.parse(localStorage.getItem("user")).uuid) {

    } else {
      this.router.navigate(['/login']);
    }
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
  }
  getList(offsetHead, offsetTail) {
    this.loading = true;
    let data = {
      "orderBy": "id",
      "IsDesc": true,
      "offsetHead": offsetHead,
      "offsetTail": offsetTail
    }
    this.maJiaService.getList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        this.loading = false;
        this.dataSource = list.data.list;
        this.totalItems = list.data.count;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });
  }

  onPageChange(event: any): void {
    this.currentPage = event.currentPage;
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
  }


  onSelect(data) {
    console.log(data)
  }
  handleModify(data) {
    console.log(data)
  }
  handleDelete(data) {
    console.log(data)
  }
  privew(data) {
    this.user_uuid = data.uuid;
    this.getPicList();
    this.privewPic = data.email
    this.showWin = true;
    this.picTotalItems = data.images;
  }
  onPicPageChange(event): void {
    this.picCurrentPage = event.currentPage;
    this.getPicList();
  }
  closeWin() {
    this.showWin = false;
    this.picCurrentPage = 1;
  }
  getPicList() {
    this.loading = true;
    let data = {
      "orderBy": "id",
      "IsDesc": true,
      "offsetHead": (this.picCurrentPage - 1) * this.picPageSize,
      "offsetTail": this.picCurrentPage * this.picPageSize,
      "user_uuid": this.user_uuid
    }
    this.photoService.getList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        this.loading = false;
        this.picDataSource = list.data.list;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
  }
}



