import { Component } from '@angular/core';
import { filterService } from './filter.service';
import { DaterangepickerConfig } from 'ng2-daterangepicker';
import { Router } from '@angular/router';
var moment = require('moment');
@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.less']
})

export class FilterComponent {
  errorMessage: string;
  firstDay = new Date(new Date().getFullYear(), new Date().getMonth(), 1);
  day = new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0);
  lastdate = new Date(new Date().getFullYear(), new Date().getMonth(), this.getLastDay(new Date().getFullYear(), new Date().getMonth()+1));
  naturesList:any = [];
  complete:boolean = false;
  calendarBoxLoading:boolean = true;
  editBoxLoading:boolean = true;
  timeFlowLoading:boolean=true;
  language:string = "CN";
  opaction={
    message:"",
    type:"success",
    showAlert:false
  };
  currentPage=1;
  preview:number=0;
  errorLog:boolean=false;
  showDays;
  editDay;
  upload:any={}
 titleDate=moment(this.firstDay).format("YYYY年M月")
   public daterange: any = {};
  
  constructor(private router:Router,private filterService: filterService,private daterangepickerOptions: DaterangepickerConfig) { 

    this.daterangepickerOptions.settings = {
            locale: { format: 'YYYY-MM-DD',
           applyLabel: "应用",
           cancelLabel: "取消",
           "daysOfWeek": [
            "日",
            "一",
            "二",
            "三",
            "四",
            "五",
            "六"
            ], },
            alwaysShowCalendars: false,      
        };
  }
 
  getLastDay(year,month)   
{   
 var new_year = year;  //取当前的年份   
 var new_month = month++;//取下一个月的第一天，方便计算（最后一天不固定）   
 if(month>12)      //如果当前大于12月，则年份转到下一年   
 {   
 new_month -=12;    //月份减   
 new_year++;      //年份增   
 }   
 var new_date = new Date(new_year,new_month,1);        //取当年当月中的第一天   
 return (new Date(new_date.getTime()-1000*60*60*24)).getDate();//获取当月最后一天日期   
}  
  ngOnInit() {
     if(localStorage.getItem("user")&&JSON.parse(localStorage.getItem("user")).uuid){
      // this.router.navigate(['/content'+this.url]);
    }else{
      this.router.navigate(['/login']);
    }
    this.showDays=new Date();
    this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31);
  }
showErrorLog(data){
  this.errorLog=data;
}
   private selectedDate(value: any) {
        this.daterange.start = value.start;
        this.daterange.end = value.end;
        this.timeFlowLoading=true;
        if(moment(this.daterange.start).format("YYYY年M月")!=moment(this.daterange.end).format("YYYY年M月")){
          this.titleDate=moment(this.daterange.start).format("YYYY年M月")+"~"+moment(this.daterange.end).format("YYYY年M月")
        }else{
          this.titleDate=moment(this.daterange.start).format("YYYY年M月")
        }        
        this.getlist(moment(this.daterange.start._d).format(), moment(this.daterange.end._d).format(),0,200)
    }
  changeLanguage(data) {
    this.language = data;
    this.calendarBoxLoading = true;
     this.timeFlowLoading = true;
    this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31)
  }
  onPreview(data){
    this.preview=data;
  }
  onChooseDay(data) {
    if (data.info) {
      this.editDay = data.info;
    } else {
      this.editDay = { apply_at: data };
    }
  }

uploadFilter(e){
  var windowURL = window.URL;
      this.upload.filter_file_url = windowURL.createObjectURL(e.target.files[0]);
   var formData = new FormData();
      formData.append("upload", e.target.files[0]);
      this.calendarBoxLoading = true;
      this.editBoxLoading = true;
      this.filterService.importFilter(formData).subscribe(list => {
        this.upload.filter_file_url = "";
        if (list.data) {
          if(list.data.error>0){
            this.showAlert("共"+list.data.total+"条记录，新增"+list.data.created+"条，更新"+list.data.updated+"条，失败"+list.data.error+"条,详情见错误日志","error");
          }else{
                 this.showAlert("共"+list.data.total+"条记录，新增"+list.data.created+"条，更新"+list.data.updated+"条，失败0条","success");       
          }
        }else if (list.errors) {         
          this.showAlert(list.errors[0].message,"error");
        }
         this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31)
      },
        error => {
          this.calendarBoxLoading = false;
          this.editBoxLoading = false;
          this.showAlert("服务器挂了，请联系开发部","error");
        });
}
uploadMood(e){
   this.calendarBoxLoading = true;
   this.editBoxLoading = true;
    var windowURL = window.URL;
    this.upload.mood_file_url = windowURL.createObjectURL(e.target.files[0]);
    var formData = new FormData();
      formData.append("upload", e.target.files[0]);
      this.filterService.importMood(formData).subscribe(list => {
        this.upload.mood_file_url = "";
        if (list.data) {
          if(list.data.error>0){
            this.showAlert("共"+list.data.total+"条记录，新增"+list.data.created+"条，更新"+list.data.updated+"条，失败"+list.data.error+"条,详情见错误日志","error");
          }else{
                 this.showAlert("共"+list.data.total+"条记录，新增"+list.data.created+"条，更新"+list.data.updated+"条，失败0条","success");       
          }
        }else if (list.errors) {         
          this.showAlert(list.errors[0].message,"error");
        }
         this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31)
      },
        error => {
           this.showAlert("服务器挂了，请联系开发部","error");
           this.calendarBoxLoading = false;
  this.editBoxLoading = false;
        });
}
uploadNature(e){
     this.calendarBoxLoading = true;
  this.editBoxLoading = true;
   var windowURL = window.URL;
    this.upload.nature_file_url = windowURL.createObjectURL(e.target.files[0]);
   var formData = new FormData();
      formData.append("upload", e.target.files[0]);
      this.filterService.importNature(formData).subscribe(list => {
        this.upload.nature_file_url = "";
        if (list.data) {
          if(list.data.error>0){
            this.showAlert("共"+list.data.total+"条记录，新增"+list.data.created+"条，更新"+list.data.updated+"条，失败"+list.data.error+"条,详情见错误日志","error");
          }else{
                 this.showAlert("共"+list.data.total+"条记录，新增"+list.data.created+"条，更新"+list.data.updated+"条，失败0条","success");
       
          }
        }else if (list.errors) {         
          this.showAlert(list.errors[0].message,"error");
        }
         this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31)
      },
        error => {
          this.calendarBoxLoading = false;
  this.editBoxLoading = false;
           this.showAlert("服务器挂了，请联系开发部","error");
        });
}
  onChangeDate(firstDay) {
    let month = new Date(new Date(firstDay).getFullYear(), new Date(firstDay).getMonth(), 0)
    this.lastdate = new Date(new Date(firstDay).getFullYear(), new Date(firstDay).getMonth(),this.getLastDay(new Date(firstDay).getFullYear(), new Date(firstDay).getMonth()+1));
    this.firstDay = firstDay;
    this.calendarBoxLoading = true;
    this.showDays=new Date(new Date(firstDay).getFullYear(), new Date(firstDay).getMonth(),1);
    this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31);
  }
  onSaveEdit(data) {
   this.showDays=this.editDay.apply_at
    this.editBoxLoading = true;
    let arr = []
    if (this.editDay.id) {
      data.id = this.editDay.id
      arr = [data];
      this.filterService.updateEdit(arr).subscribe(list => {
        if (list.data) {
          this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31)
          this.showAlert("保存成功","success")
        }
        if (list.errors) {
          this.editBoxLoading = false;
          this.errorMessage = list.errors[0].message;
          this.showAlert(this.errorMessage,"error");
        }
        
      },
        error => {
          this.errorMessage = "服务器异常，请联系工程部！";
          this.showAlert(this.errorMessage,"error")
        });
    } else {
      arr = [data];
      this.filterService.createEdit(arr).subscribe(list => {
        if (list.data) {
          this.showAlert("保存成功","success")
          this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31)
        }
        if (list.errors) {
          this.editBoxLoading = false;
          this.errorMessage = list.errors[0].message;
          this.showAlert(this.errorMessage,"error")
        }
      },
        error => {
          this.errorMessage = "服务器异常，请联系工程部！";
          this.showAlert(this.errorMessage,"error")
        });
    }
  }
  showAlertChildren(data){
this.showAlert(data.message,data.type)
  }
  showLoadingChildren(data){
    this.showAlert(data.message,data.type)
    this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31)
  }
  showAlert(message:string,type:string){
            this.opaction={
    message:message,
    type:type,
    showAlert:true
  }
  this.opaction.showAlert=true;
  let that=this;
            setTimeout(function(){
              that.opaction.showAlert=false;
            },2000)
  }
  childGetList(){
    this.editBoxLoading = true;
    this.getlist(moment(this.firstDay).format(), moment(this.lastdate).format(),0,31)
          this.showAlert("重置成功","success")
  }
  getlist(startTime, endTime,offsetHead,offsetTail) {
    let data = {
      "orderBy": "apply_at",
      "IsDesc": false,
      "offsetHead": offsetHead,
      "offsetTail": offsetTail,
      "startTime": startTime,
      "endTime": endTime,
      "where": {
        "lang": this.language
      }
    }
    this.filterService.getList(data).subscribe(list => {
      this.editBoxLoading = false;
      this.calendarBoxLoading = false;
      this.timeFlowLoading = false;
      
      this.editDay={apply_at:this.showDays};
      if (list.data) {
        this.naturesList = list.data.list
        this.naturesList.map((item, i) => {
          // console.log(moment(item.apply_at).format('YYYY-MM-DD'))
          if (moment(item.apply_at).format('YYYY-MM-DD') == moment(this.showDays).format('YYYY-MM-DD')) {
            this.editDay = item;
            this.complete = true;
          }
        })
      }
    },
      error => {
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage,"error")
      });
  }
}



