import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { majia } from './../../../config';
import axios from 'axios';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
@Injectable()

export class maJiaService {
    private addURL: string = majia.add;
private listURL: string = majia.list;
private photo_tokenURL: string = majia.photo_token;

private livephoto_tokenURL: string = majia.livephoto_token;
private upload_photoURL: string = majia.upload_photo;
    constructor(private http: Http) { }
    getToken(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': data.author_token ,"X-Tuso-Device-Token": data.device_token});
        let options = new RequestOptions({ headers: headers });
        return this.http.get(this.photo_tokenURL,options)
            .map(this.extractData)
            .catch(this.handleError);
    }
getLivephotoToken(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': data.author_token ,"X-Tuso-Device-Token": data.device_token});
        let options = new RequestOptions({ headers: headers });
        return this.http.get(this.livephoto_tokenURL,options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    upload(data: any): Observable<any> {       
        return this.http.post(this.upload_photoURL, data)
            .map(this.extractData)
            .catch(this.handleError);
    }
    addPup(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token ,"Content-Type": 'application/x-www-form-urlencoded'});
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.addURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
getList(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.listURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    private extractData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }
}


