import { Component, Input, Output, EventEmitter } from '@angular/core';
import { DaterangepickerConfig } from 'ng2-daterangepicker';
import { region } from './../../../../config';
import _ from "underscore"
var moment = require('moment');
import { newActivityService } from "./newActivity.service"
@Component({
  selector: 'app-newActivity',
  templateUrl: './newActivity.component.html',
  styleUrls: ['./newActivity.component.less']
})
export class NewActivityComponent {
  @Input() windowWidht;//窗口宽度
  @Output() closeWindow = new EventEmitter<any>();//关闭窗口
  @Output() save = new EventEmitter<any>();//保存
  @Input() addInfo;//修改信息
  regions = region;//区域
  constructor(private daterangepickerOptions: DaterangepickerConfig, private newActivityService: newActivityService) {
//时间选择构造
    this.daterangepickerOptions.settings = {
      locale: {
        format: 'YYYY-MM-DD HH:mm:ss',
        applyLabel: "应用",
        cancelLabel: "取消",
        "daysOfWeek": [
          "日",
          "一",
          "二",
          "三",
          "四",
          "五",
          "六"
        ],
      },
      "timePicker": true,
      "timePicker24Hour": true,
      "timePickerSeconds": true,
      "autoApply": true,
      alwaysShowCalendars: false,
    };
  }
  //组件更新
  ngOnChanges() {
    if (this.addInfo.start_time) {
      this.daterangepickerOptions.settings = {
        locale: {
          format: 'YYYY-MM-DD HH:mm:ss',
          applyLabel: "应用",
          cancelLabel: "取消",
          "daysOfWeek": [
            "日",
            "一",
            "二",
            "三",
            "四",
            "五",
            "六"
          ],
        },
        "startDate": moment(this.addInfo.start_time).format("YYYY-MM-DD HH:mm:ss"),
        "endDate": moment(this.addInfo.end_time).format("YYYY-MM-DD HH:mm:ss"),
        "timePicker": true,
        "timePicker24Hour": true,
        "timePickerSeconds": true,
        "autoApply": true,
        alwaysShowCalendars: false,
      };
    }
  }
  //提交并整个数据
  handleSub() {
    let data: any = {
      start_time: this.addInfo.start_time,
      end_time: this.addInfo.end_time,
      to_url: this.addInfo.to_url,
      description: this.addInfo.description,
      title: this.addInfo.title,
      img_name: this.addInfo.image_url,
      region: this.addInfo.region,
      status: this.addInfo.status ? this.addInfo.status : "ActivityStatusOnline"
    }
    if (this.addInfo.status = "ActivityStatusEnd") {
      data.status = "ActivityStatusOnline"
    }
    if (this.addInfo.id) {
      data.id = this.addInfo.id;
    }
    if (this.addInfo.image_file) {
      var formData = new FormData();
      formData.append("upload", this.addInfo.image_file);
      this.newActivityService.upLoadCover(formData).subscribe(list => {
        if (list.data) {
          data.img_name = list.data.file_name;
          this.save.emit(data);
        }
        if (list.errors) {
          let opaction = {
            message: list.errors[0].message,
            type: "error",
          }
        }
      },
        error => {
          let opaction = {
            message: "服务器挂了，请联系开发部",
            type: "error",
          }
        });
    } else {
      this.save.emit(data);
    }
  }
  //选择日期
  selectedDate(value: any) {
    this.addInfo.start_time = moment(value.start._d).format();
    this.addInfo.end_time = moment(value.end._d).format();

  }
  //更改封面图片
  changeCover(e) {
    var windowURL = window.URL;
    this.addInfo.image_url = windowURL.createObjectURL(e.target.files[0]);
    this.addInfo.image_file = e.target.files[0]

  }
  //地区是否选中
  hasRegion(data) {
    var even = _.find(this.addInfo.region, function (num) {
      return num == data.ID;
    });
    return even;
  }
  //选择日期
  chooseRegion(data) {
    var even = _.find(this.addInfo.region, function (num) {
      return num == data.ID;
    });
    if (even) {
      this.addInfo.region = _.without(this.addInfo.region, data.ID);
    } else {
      this.addInfo.region.push(data.ID)
    }
  }
}



