import { Component } from '@angular/core';
import { activityService } from './activity.service';
import { Router } from '@angular/router';
var _ = require('underscore');

var moment = require('moment');
@Component({
  selector: 'app-activity',
  templateUrl: './activity.component.html',
  styleUrls: ['./activity.component.less']
})
export class ActivityComponent {
  status = "ActivityStatusEnd";
  loading = true;
  alert = {
    type: "success",
    message: "success"
  }
  errorMessage = ""
  showWindow = false;
  totalItems = 0;
  activityList = [];
  currentPage = 1;
  chooses = [];
  editInfo;
  saveStatus = 0;
  tip = false;
  addInfo = {
    description: "",
    to_url: "",
    region: [],
    start_time: "",
    end_time: "",
    image_url: "",
    title: ""
  };
  constructor(private activityService: activityService,private router:Router) { }
  changeStatus(data) {
    this.status = data;
    this.loading = true;
    this.getlist(0, 6, this.status);
  }
  ngOnInit() {
    this.getlist(0, 6, this.status);
  }
  pageChanged(event) {
    this.currentPage = event;
    this.loading = true;
    this.getlist((event - 1) * 6, event * 6, this.status);
  }
  closeWindow() {
    this.showWindow = false;
  }
  openWindow(data) {

    if (data) {
      this.addInfo = data;
      this.saveStatus = 1;
    } else {
      this.addInfo = {
        description: "",
        to_url: "",
        region: [],
        start_time: "",
        end_time: "",
        image_url: "",
        title: ""
      }
    }
    this.showWindow = true;
  }
  chooseCards(data) {
    var even = _.find(this.chooses, function (num) {
      return num == data.id;
    });
    if (!even) {
      this.chooses.push(data.id);
    } else {
      this.chooses = _.without(this.chooses, data.id)
    }
  }
  hasChoose(data) {
    var even = _.find(this.chooses, function (num) {
      return num == data.id;
    });
    return even;
  }
  save(data) {
    this.showWindow = false;
    this.loading = true;
    let activitys = [data]
    if (this.saveStatus == 0) {
      this.activityService.addActivity(activitys).subscribe(list => {
        this.loading = false;
        if (list.data) {
          this.getlist(0, 6, this.status);
          this.showAlert("修改成功", "success")
        } if (list.errors) {
          this.loading = false;
          this.errorMessage = list.errors[0].message;
          this.showAlert(this.errorMessage, "error")
        }
      },
        error => {
          this.errorMessage = "服务器异常，请联系工程部！";
          this.showAlert(this.errorMessage, "error")
        });
    } else {
      this.activityService.changeActivity(activitys).subscribe(list => {
        this.loading = false;
        if (list.data) {

          this.getlist(0, 6, this.status);
          this.showAlert("修改成功", "success")
        }
        if (list.errors) {
          this.loading = false;
          this.errorMessage = list.errors[0].message;
          this.showAlert(this.errorMessage, "error")
        }
      },
        error => {
          this.errorMessage = "服务器异常，请联系工程部！";
          this.showAlert(this.errorMessage, "error")
        });
    }
  }
  changeState(status, type) {
    //type 0:批量 1:单个
    this.showWindow = false;
    this.loading = true;
    let data = []
    if (type == 0) {
      if (status == "ActivityStatusOffline") {
        status = "ActivityStatusOnline"
      } else if (status == "ActivityStatusOnline") {
        status = "ActivityStatusOffline"
      }
      this.chooses.map((item, i) => {
        let temp: any = {}
        temp.id = item;
        temp.status = status
        data.push(temp)
      })
      this.activityService.changeActivity(data).subscribe(list => {

        if (list.data) {
          this.getlist(0, 6, this.status);
          this.showAlert("修改成功", "success")
        }
        if (list.errors) {
          this.loading = false;
          this.errorMessage = list.errors[0].message;
          this.showAlert(this.errorMessage, "error")
        }
      },
        error => {
          this.errorMessage = "服务器异常，请联系工程部！";
          this.showAlert(this.errorMessage, "error")
        });
    } else if (type == 1) {
      data = [status]
      this.activityService.changeActivity(data).subscribe(list => {

        if (list.data) {
          this.getlist(0, 6, this.status);
          this.showAlert("修改成功", "success")
        }
        if (list.errors) {
          this.loading = false;
          this.errorMessage = list.errors[0].message;
          this.showAlert(this.errorMessage, "error")
        }
      },
        error => {
          this.errorMessage = "服务器异常，请联系工程部！";
          this.showAlert(this.errorMessage, "error")
        });
    }

  }
  changeStatuClone(data) {
    this.addInfo = data;
    this.saveStatus = 0;
    this.showWindow = true;
  }
  showAlert(message: string, type: string) {
    this.alert = {
      message: message,
      type: type
    }
    this.tip = true;

    let that = this;
    setTimeout(function () {
      that.tip = false;
    }, 2000)
  }
  getlist(offsetHead, offsetTail, status) {
    let data = {
      "orderBy": "_id",
      "IsDesc": true,
      "offsetHead": offsetHead ? offsetHead : 0,
      "offsetTail": offsetTail ? offsetTail : 6,
      "where": {
        "status": status
      }
    };

    this.activityService.getActivityList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        this.activityList = list.data.list
        this.totalItems = list.data.count
      }
      if (list.errors) {
        this.loading = false;
        this.errorMessage = list.errors[0].message;
        this.showAlert(this.errorMessage, "error")
      }
    },
      error => {
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });


  }
}



