import { Component, Input, Output, EventEmitter } from '@angular/core';
import { setCalendarService } from './setCalendar.service';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
// Observable class extensions
import 'rxjs/add/observable/of';
// Observable operators
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
var moment = require('moment');
@Component({
  selector: 'app-setCalendar',
  templateUrl: './setCalendar.component.html',
  styleUrls: ['./setCalendar.component.less']
})
export class SetCalendarComponent {
  @Input() editDay;
  @Input() complete;
  @Input() language;
  @Output() onSaveEdit = new EventEmitter<any>();
  @Output() showAlertChildren = new EventEmitter<any>();
  @Output() childGetList = new EventEmitter<any>();
  @Output() showLoadingChildren = new EventEmitter<any>();
  canEdit: boolean = false;
  title: string;
  filterNum: string;
  filterImg: string;
  moonNum: string;
  moonImg: string;
  depict: string;
  logion: string;
  showLarge: boolean = false;
  errorType: number;
  editWindow: boolean = false;
  editWindowData: any;
  filterList: any = [];
  moodList = []
  naturesId;
  private searchTermStream = new Subject<string>();
  constructor(private setCalendarService: setCalendarService) { }
  ngOnInit() {
    // console.log(moment().format('MMMM Do YYYY, h:mm:ss a'))
    this.title = moment(new Date()).format('YYYY年M月D日')

  }
  showEdit() {
    this.editWindowData = this.editDay;
    this.editWindow = true;
  }
  hideWindow() {
    this.editWindow = false;
  }
  editSave(opaction) {
    if (opaction.type != "error") {
      this.editWindow = false;
    }
    this.showAlertChildren.emit(opaction);
  }
  deleteSave(opaction) {
    if (opaction.type != "error") {
      this.editWindow = false;
    }
    this.showLoadingChildren.emit(opaction);
  }
  edit() {
    if (this.editDay && this.editDay.apply_at) {
      this.canEdit = true;
    }
  }
  delete() {
    let data = [this.editDay.id]
    this.setCalendarService.deleteNatures(data)
      .then(res => {
        if (res.success) {
          this.childGetList.emit();
        }
      });
  }
  save() {
    if (this.moonImg != "" && this.filterImg != "" && this.filterNum != this.moonNum) {
      this.errorType = 1;
      return;
    } else if (this.depict == "") {
      this.errorType = 2;
      return;
    } else if (this.logion == "") {
      this.errorType = 3;
      return;
    }
    this.errorType = 0;
    let data: any = {};
    data.apply_at = moment(this.editDay.apply_at).format();
    data.depict = this.depict;
    data.logion = this.logion;
    data.image_filter_num = this.filterNum;
    data.mood_num = this.moonNum;
    data.lang = this.language;
    this.onSaveEdit.emit(data);
    this.canEdit = false;
  }
  cancel() {
    this.canEdit = false;
    this.dataInit();
  }
  ngOnChanges() {
    this.dataInit();
    this.canEdit = false;
  }
  showLargeBox(data) {
    if (data) {
      this.showLarge = data;
    } else {
      this.showLarge = !this.showLarge;
    }
  }
  saveSuccess(data) {
    this.moonNum = data.mood.moonNum ? data.mood.moonNum : this.moonNum;
    this.filterNum = data.filter.filterNum ? data.filter.filterNum : this.filterNum;
    this.moonImg = data.mood.image_url ? data.mood.image_url : this.moonImg;
    this.filterImg = data.filter.image_url ? data.filter.image_url : this.filterImg;
  }
  dataInit() {
    this.title = this.editDay && this.editDay.apply_at ? this.language == "CN" ? moment(this.editDay.apply_at).format('YYYY年M月D日') : moment(this.editDay.apply_at).format('YYYY-M-D') : "";
    this.filterNum = this.editDay && this.editDay.image_filter_num ? this.editDay.image_filter_num : '';
    this.moonNum = this.editDay && this.editDay.mood_num ? this.editDay.mood_num : '';
    this.depict = this.editDay && this.editDay.depict ? this.editDay.depict : '';
    this.logion = this.editDay && this.editDay.logion ? this.editDay.logion : '';
    this.filterImg = this.editDay && this.editDay.image_filter ? this.editDay.image_filter.image_url : '';
    this.moonImg = this.editDay && this.editDay.mood ? this.editDay.mood.image_url : '';
    this.naturesId = this.editDay && this.editDay.id ? this.editDay.id : null;
  }
  chooseFilter(e, data) {
    e.stopPropagation();
    this.filterList = [];

    this.editDay.image_filter = data;
    this.moonNum = data.number;
    this.filterNum = data.number;
    this.filterImg = data.image_url;
    this.moodList.map((item, i) => {
      if (item.number == data.number) {
        this.editDay.mood = item;
        this.moonImg = item.image_url;
      }
    })
    this.moodList = [];
  }
  search(term: string) {
    this.moonNum = term;
    let data = {
      "orderBy": "id",
      "IsDesc": true,
      "offsetHead": 0,
      "offsetTail": 10,
      "keywords": term
    }
    this.errorType = 0;
    this.setCalendarService.getMoodList(data).subscribe(list => {
      if (list.data) {
        if (list.data.list.length > 1) {
          this.moodList = list.data.list;
          this.moonImg = "";
        } else
          if (list.data.list.length == 1) {
            this.moonImg = list.data.list[0].image_url;
            this.editDay.mood = list.data.list[0];

          } else {
            this.moonImg = "";
          }
      }
    },
      error => {
      });
    this.setCalendarService.getFilterList(data).subscribe(list => {
      if (list.data) {
        if (list.data.list.length > 1) {
          this.filterList = list.data.list;
          this.filterImg = "";
        } else if (list.data.list.length == 1 && list.data.list[0].number == term) {
          this.filterImg = list.data.list[0].image_url;
          this.editDay.image_filter = list.data.list[0];
          this.filterList = list.data.list;
        } else {
          this.filterImg = "";
          this.filterList = [];
        }
      }
    },
      error => {
      });
  }
  handleBlur(e) {
    this.filterList = [];
  }
}




