import { Component, Input, Output, EventEmitter } from '@angular/core';
var moment = require('moment');
var _ = require('underscore');
import { region } from './../../../../config';
@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.less']
})
export class CardComponent {
  @Input() activity;//活动对象 object
  @Input() chooses;//是否选中
  @Output() openWindow = new EventEmitter<any>();//打开编辑窗口
  @Output() chooseCards = new EventEmitter<any>();//选择卡片
  @Output() changeStatus = new EventEmitter<any>();//改变状态
  @Output() changeStatusClone = new EventEmitter<any>();//克隆卡片
  startTime;
  endTime;
  //选择卡片
  chooseCard(data) {
    this.chooseCards.emit(data);
  }
  //更改状态
  changeState(e, status) {
    e.stopPropagation();
    if (status == "ActivityStatusOffline") {
      status = "ActivityStatusOnline"
    } else if (status == "ActivityStatusUnderway") {
      status = "ActivityStatusOffline"
    }
    var data = {
      status: status,
      id: this.activity.id
    }
    this.changeStatus.emit(data);
  }
  //克隆
  changeStateClone(e, data) {
    e.stopPropagation();
    this.changeStatusClone.emit(data);
  }
  //组件发生状态改变
  ngOnChanges() {
    this.startTime = moment(this.activity.start_time).format("YYYY.MM.DD")
    this.endTime = moment(this.activity.end_time).format("YYYY.MM.DD")
  }
}



