import { Component, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { DaterangepickerConfig } from 'ng2-daterangepicker';
import { ErrorLogService } from "./errorLog.service";
var moment = require('moment');
@Component({
  selector: 'app-errorLog',
  templateUrl: './errorLog.component.html',
  styleUrls: ['./errorLog.component.less']
})
export class ErrorLogComponent {
  @Output() showErrorLog = new EventEmitter<any>();//是否打开错误日志
  collection = [];//错误日志列表
  totalItems = 0;//错误日志数量
  page = 1;//第几页
  count = 6;//煤业显示多少
  loading: boolean = true;//加载层
  moreLoading: boolean = true;//查看更多加载层
  //日历选择
  daterange = {
    start: "",
    end: ""
  };

  currentPage = 1;//分页插件
  moreData = [];//更多错误信息列表
  moreWindow = false//更多错误信息窗口是否显示
  status = "Nature";//错误信息查看类型

//构造
  constructor(private ErrorLogService: ErrorLogService, private daterangepickerOptions: DaterangepickerConfig) {
//日历插件初始化
    this.daterangepickerOptions.settings = {
      locale: {
        format: 'YYYY-MM-DD',
        applyLabel: "应用",
        cancelLabel: "取消",
        "daysOfWeek": [
          "日",
          "一",
          "二",
          "三",
          "四",
          "五",
          "六"
        ]
      },
      alwaysShowCalendars: false,
    };
  }
  //查看更多
  lookMore(data) {
    this.moreWindow = true;
    this.moreLoading = true;
    let list = [data];
    this.moreData = [];
    this.ErrorLogService.getMoreInfo(list).subscribe(list => {
      this.moreLoading = false;
      if (list.data) {
        // this.activityList = list.data.list
        // this.moreData = list.data.count
        this.moreData = list.data[0];
      }
    },
      error => {
        // this.errorMessage = "服务器异常，请联系工程部！";
        // this.showAlert(this.errorMessage,"error")
      });
  }
  //初始化加载
  ngOnInit() {
    this.getlist((this.page - 1) * this.count, this.page * this.count, this.status, this.daterange.start == "" ? "" : moment(this.daterange.start).format(), this.daterange.end == "" ? "" : moment(this.daterange.end).format());
  }
  //关闭查看更多窗口
  closeMoreWindow() {
    this.moreWindow = false;
  }
  //关闭错误日志窗口
  closeWindow() {
    this.showErrorLog.emit(false);
  }
  //更改查看类型
  changeStatus(data) {
    this.status = data;
    this.page = 1;
    //   this.daterange={
    //   start:"",
    //   end:""
    // };
    this.getlist((this.page - 1) * this.count, this.page * this.count, this.status, this.daterange.start == "" ? "" : moment(this.daterange.start).format(), this.daterange.end == "" ? "" : moment(this.daterange.end).format());
  }
  //获取错误日志信息
  getlist(offsetHead, offsetTail, status, startTime, endTime) {
    this.loading = true;
    let data = {
      "orderBy": "_id",
      "IsDesc": true,
      "offsetHead": offsetHead ? offsetHead : 0,
      "offsetTail": offsetTail ? offsetTail : 6,
      "startTime": startTime,
      "endTime": endTime,
      "where": {
        "type": status
      }
    };

    this.ErrorLogService.getErrorList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        // this.activityList = list.data.list
        this.totalItems = list.data.count
        this.collection = list.data.list;
      }
    },
      error => {
        // this.errorMessage = "服务器异常，请联系工程部！";
        // this.showAlert(this.errorMessage,"error")
      });


  }
  //更改查询日期
  selectedDate(value: any) {
    this.daterange.start = value.start._d;
    this.daterange.end = value.end._d;
    this.getlist((this.page - 1) * this.count, this.page * this.count, this.status, moment(this.daterange.start).format(), moment(this.daterange.end).format())
  }
  //更改页数
  pageChanged(e) {
    this.page = e;
    this.getlist((this.page - 1) * this.count, this.page * this.count, this.status, this.daterange.start, this.daterange.end);
  }
}



