import { Component, ViewEncapsulation } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser'
import { DaterangepickerConfig } from 'ng2-daterangepicker';
import { photoService } from "./photo.service"
import { Router } from '@angular/router';
var moment = require('moment');
@Component({
  selector: 'app-photo',
  templateUrl: './photo.component.html',
  styleUrls: ['./photo.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class PhotoComponent {
  constructor(private router:Router,private daterangepickerOptions: DaterangepickerConfig, private _sanitizer: DomSanitizer, private photoService: photoService) {

    this.daterangepickerOptions.settings = {
      locale: {
        format: 'YYYY-MM-DD',
        applyLabel: "应用",
        cancelLabel: "取消",
        autoUpdateInput: false,
        "daysOfWeek": [
          "日",
          "一",
          "二",
          "三",
          "四",
          "五",
          "六"
        ],
      },
      alwaysShowCalendars: false,
    };
  }
  playerUrl=""
  privewWin=false;
  dataTagSource = [];
  tagCurrentPage = 1;
  tagPageSize = 200;
  startTime = "";
  endTime = "";
  status = "";
  window = false;
  loading = false;
  statusValue: any = {
    name: ""
  };
  userBy = "";
  currentPage = 1;
  pageSize = 10;
  illegal=false;
  searchIllegal:any=""
  setWin=false;
  dataSource = [];
  totalItems = 0;
  tip = false;
  checkArray = [];
  select = [];
  tags = [];
  selectTags = [];
  tagsEdit = false;
  totalTagItems=0;
  userTag=[];
  selectTag=false;
  userSelect;
  privewPhoto;
    orderStatus = [
      {
    "id": "0",
    "status": "",
    "name": "所有"
  },{
    "id": "1",
    "status": false,
    "name": "合法"
  }, {
    "id": "2",
    "status": true,
    "name": "不合法"
  }]
  config = {
    select: false,//是否需要单选框
    headStyle: 'fixed',//是否固定顶部
    serial: true,//是否有序号
    currentPage: this.currentPage,
    pageSize: this.pageSize
  }
  alert = {
    message: "",
    type: "success"
  }
  columns = [{
    title: '预览图',
    dataIndex: 'lite_image',
    width: 150,
    render: (text) => {
      return this.getDom(`
       <div class="cover" data-event=custom2 style="background-image:url('${text.url}')"></div>
	  			`);
    }
  }, {
    title: '使用滤镜',
    dataIndex: 'display_image',
    width: 150,
    render: (text) => {
     
     return this.getDom(`
       <span>${text.filter_num?text.filter_num:'无'}</span>
	  			`);

    }
  }, {
    title: '随记',
    dataIndex: 'note',
    width: 150,
    render: (text) => {
     return this.getDom(`
       <span>${text?text.title:'无'}</span>
	  			`);

    }
  }, {
    title: '发布时间',
    dataIndex: 'timestamp',
    width: 150,
    render: (text) => {
     return this.getDom(`
     
       <span>${moment(text).format("YYYY-MM-DD HH:mm:ss")}</span>
	  			`);

    }
  }, {
    title: '发布人ID',
    dataIndex: 'user',
    width: 100,
    render: (text) => {
     return this.getDom(`
       <span>${text.id}</span>
	  			`);

    }
  }, {
    title: '拍照地点',
    dataIndex: 'metadata',
    width: 100,
    render: (text) => {
     return this.getDom(`
       <span>${text.weatherInfo&&text.weatherInfo.location?text.weatherInfo.location.name:'无'}</span>
	  			`);

    }
  }, {
    title: '照片标签',
    dataIndex: 'tags',
    width: 150,
    render: (text) => {
      
      let resule=""
      if(text.length==0){
        resule="暂无标签";
      }
      text.map((item,i)=>{
        if(i==0){
        resule+=item.name;
        }else{
          resule+="、"+item.name;
        }
})
       return this.getDom(`
       <span>${resule}</span>
	  			`);

      
    }
  }, {
    title: '操作',
    dataIndex: 'is_illegal',
    width: 150,
    render: (text) => {
      return this.getDom(`
		  			<a data-event=custom1 style="color: #3D95D1;text-decoration: underline;cursor: pointer;">编辑标签</a>
		  			<a data-event=custom3 style="color: #E94646;text-decoration: underline;cursor: pointer;margin-left:20px;">设为${text?'合':'违'}法</a>
	  			`);
    }
  }]
  getDom(str) {
    return this._sanitizer.bypassSecurityTrustHtml(str)
  }
  ngOnInit() {
     if(localStorage.getItem("user")&&JSON.parse(localStorage.getItem("user")).uuid){
        
      }else{
      this.router.navigate(['/login']);
    }
    this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
   this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
       }
   selectedDate(value: any) {
    this.startTime = moment(value.start._d).format();
    this.endTime = moment(value.end._d).format();
   this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
      
  }
  reset() {
  this. startTime = "";
  this.endTime = "";
  this.currentPage=1;
  this.userBy="";
   this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
      
  }
  serchUserBy() {
  this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
       }
  handleModify(data) {

  }
  keywordSearch(){
    
  }
  onSelect(data) {
    this.select = data;
  }
  custom3(data){
    this.select=[data.id];
    this.illegal=data.is_illegal
this.setWin=true;
}
 changeUserStatus(data){
   console.log(data)
   this.statusValue=data.name;
   this.searchIllegal=data.status;
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
      
 }
  setDeillegal(){
     this.loading = true;
     if(this.illegal){
  this.photoService.setDeillegal(this.select).subscribe(list => {
    this.setWin=false;
      this.loading = false;
      if (list.data) {
        this.showAlert("合法成功", "success")
        this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.setWin=false;
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      }); 
     }else{
         this.photoService.setIllegal(this.select).subscribe(list => {
           this.setWin=false;
      this.loading = false;
      if (list.data) {
        this.showAlert("设置非法成功", "success")
        
        this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.setWin=false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      }); 
     }


  }
  export() {
    this.window = true;
  }
  closeWindow() {
    this.window = false;
  }
  onPageChange(data) {
    if (!data.currentPage) {
      data.currentPage = 1;
    }
    this.config.currentPage = data.currentPage;
    this.currentPage = data.currentPage;
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
      
  }
  custom1(data) {
    this.userTag = data.tags
    this.userSelect = data.uuid
    this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
    this.selectTag = true;
  }
  filtrateSub(data) {
    this.selectTags = data;
  }
  filtrateClo(data) {
    this.selectTags = data;
  }
  editLayerShow() {
    this.tagsEdit = true;
    this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
  }
  tagsClo() {
    this.tagsEdit = false;
  }
tagsRemaneSub(data) {
    this.loading = true;
    this.photoService.renameTag(data).subscribe(list => {
      if (list.data) {
        this.showAlert("修改成功", "success")
        this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
    // this.tagsEdit = false;
  }
   pushTag(data) {
    let newData = []
    data.map((item, i) => {
      let temp: any = {};
      temp.target_uuid = this.userSelect;
      temp.tag_uuid = item;
      newData.push(temp);
    })
    this.loading = true;
    this.photoService.pushTag(newData).subscribe(list => {
      this.loading = false;
      if (list.data && list.data.success) {
        this.showAlert("修改标签成功", "success");
        
       this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
        this.selectTag = false;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
        this.selectTag = false;
      }
    },
      error => {
        this.loading = false;
        this.selectTag = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });

  }
    userTagsClo() {
    this.selectTag = false;
  }
  tagsAddList(data) {
    this.loading = true;
    this.photoService.addTag(data).subscribe(list => {
      if (list.data) {
        this.showAlert("新增成功", "success")
        this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.showAlert("服务器异常，请联系工程部！", "error")
        this.loading = false;
      });
  }
  tagsDeleteList(data) {
    this.loading = true;
    this.photoService.deleteTags(data).then(list => {
      if (list.success) {
        this.showAlert("删除", "success")
        this.getTagList((this.tagCurrentPage - 1) * this.tagPageSize, this.tagCurrentPage * this.tagPageSize);
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    }).catch(function (err) {
      this.loading = false;
      this.showAlert("服务器异常，请联系工程部！", "error")
    });
  }
  custom2(data){
    console.log(data)
    this.privewWin=true;
this.privewPhoto=data;

if(data.livephoto){
this.playerUrl=data.livephoto.livephoto_url
}
  }
  showAlert(message: string, type: string) {
    this.alert = {
      message: message,
      type: type
    }
    this.tip = true;
    let that = this;
    setTimeout(function () {
      that.tip = false;
    }, 2000)
  }
    getTagList(offsetHead, offsetTail) {
    this.loading = true;
    let data = {
      "orderBy": "current_date",
      "IsDesc": true,
      "offsetHead": offsetHead,
      "offsetTail": offsetTail
    }
    this.photoService.getTagList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        this.dataTagSource = list.data.list;
        this.totalTagItems = list.data.count;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
  }
    search(){
this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.userBy,this.selectTags)
    }

  getList(offsetHead, offsetTail, startTime, endTime, keywords,tags) {
    this.loading = true;
    let data:any = {
      "orderBy": "id",
      "IsDesc": true,
      "offsetHead": offsetHead,
      "offsetTail": offsetTail,
      "startTime": startTime,
      "endTime": endTime,
      "keywords":keywords,
      "tags":tags,
    "where":{

    }
    }
  console.log(this.searchIllegal)
    if(this.searchIllegal===""){
      
      
    }else{
console.log(this.searchIllegal)
      data.where.is_illegal=this.searchIllegal
    }
    this.photoService.getList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        this.loading = false;
        this.dataSource = list.data.list;
        this.dataSource.map((item,i)=>{
           item.metadata=JSON.parse(item.metadata);
        })
          console.log(this.dataSource)
        this.totalItems = list.data.count;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.showAlert("服务器异常，请联系工程部！", "error")
      });
  }
}



