import { Component } from '@angular/core';
import { loginService } from './login.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.less']
})
export class LoginComponent {
  errorMessage: string;
  userInfo;
  user={
  username: "",
  password: ""
}
 alert = {
    type: "success",
    message: "success"
  }
  tip = false;
  canClick=true;
  url=document.location.href.split("content")[1]?document.location.href.split("content")[1]:"/user";

  constructor(private loginService: loginService,private router:Router) { }
  ngOnInit() { 
    if(localStorage.getItem("user")&&JSON.parse(localStorage.getItem("user")).uuid){
      
      this.router.navigate(['/content'+this.url]);
    }
}
 showAlert(message: string, type: string) {
    this.alert = {
      message: message,
      type: type
    }
    this.tip = true;

    let that = this;
    setTimeout(function () {
      that.tip = false;
    }, 2000)
  }
  login() {    
    if(!this.canClick)
    return;
    this.showAlert("正在登陆","warning");
    this.canClick=false;
    this.loginService.login(this.user).subscribe(userInfo => {
      this.userInfo = userInfo;
      this.canClick=true;
      console.log(userInfo)
      if(userInfo.errors&&userInfo.errors[0].code==1){
      this.errorMessage="用户名或密码错误，请重试！"
      this.tip = false;
    }else if(userInfo.token){
        this.errorMessage=""
         this.showAlert("登陆成功","success");
        localStorage.setItem("user",JSON.stringify(userInfo))
        this.router.navigate(['/content'+this.url]);
      }
    },
      error => {
        this.canClick=true;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage,"error");
      });
  }
}



