import { Component,ViewEncapsulation } from '@angular/core';
import { filterService } from './reporttype.service';
import { Router } from '@angular/router';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser'
var moment = require('moment');
@Component({
  selector: 'app-reporttype',
  templateUrl: './reporttype.component.html',
  styleUrls: ['./reporttype.component.less'],
  encapsulation: ViewEncapsulation.None
})

export class ReportTypeComponent {
  constructor(private _sanitizer: DomSanitizer) { }

  userBy = "";
  currentPage = 1;
  pageSize = 100;
  dataSource = [];
  totalItems = 0;
  tip = false;
  config = {
    select: false,//是否需要单选框
    headStyle: 'fixed',//是否固定顶部
    serial: true,//是否有序号
    currentPage: this.currentPage,
    pageSize: this.pageSize
  }
  alert = {
    message: "",
    type: "success"
  }
  columns = [{
    title: '属性',
    dataIndex: 'id',
    width: 80
  }, {
    title: '关键词',
    dataIndex: 'nickname',
    width: 200,
    render: (text) => {
      return this.getDom(`
       <span class="${text.user_property_type}">${text.nickname}</span>
	  			`);

    }
  },  {
    title: '操作',
    dataIndex: 'unused_length',
    width: 800,
    render: (text) => {

      return this.getDom(`
      	<div><a data-event=custom5 style="color: #3D95D1;text-decoration: underline;cursor: pointer;">${text}</a></div>
		  		
	  			`);
    }

  }]
  getDom(str) {
    return this._sanitizer.bypassSecurityTrustHtml(str)
  }
   showAlert(message: string, type: string) {
    this.alert = {
      message: message,
      type: type
    }
    this.tip = true;
    let that = this;
    setTimeout(function () {
      that.tip = false;
    }, 2000)
  }
  closeWindow(){

  }
  keywordSearch() { }
  onPageChange(data) {
    if (!data.currentPage) {
      data.currentPage = 1;
    }
    this.config.currentPage = data.currentPage;
    this.currentPage = data.currentPage;
    }
  custom1(data){}
  custom2(data){}
  custom3(data){}
}



