import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { setCalendar } from './../../../config';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import axios from 'axios'
@Injectable()

export class ErrorLogService {
    private getErrorListUrl = setCalendar.error; 
private getErrorViewUrl = setCalendar.erroeView;   
    constructor(private http: Http) { }

    getErrorList(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.getErrorListUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    getMoreInfo(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.getErrorViewUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    private extractData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }
}


