import { Component, ViewEncapsulation } from '@angular/core';
import { DaterangepickerConfig } from 'ng2-daterangepicker';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser'
import { orderService } from "./order.service"
import { Router } from '@angular/router';
var moment = require('moment');
@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class OrderComponent {
  statusValue:any = {
    name:""
  };
  typeValue = "";
  startTime = "";
  endTime = "";
  status = "";
  userBy = "";
  window = false;
  orderStatus = [{
    "id": "1",
    "status": "ORDER_STATUS_NULL",
    "name": "所有类型"
  }, {
    "id": "2",
    "status": "ORDER_STATUS_NOTPAID",
    "name": "未付款"
  }, {
    "id": "3",
    "status": "ORDER_STATUS_PAYING",
    "name": "正在付款"
  }, {
    "id": "3",
    "status": "ORDER_STATUS_PAYED",
    "name": "已付款"
  }, {
    "id": "3",
    "status": "ORDER_STATUS_CLOSED",
    "name": "订单取消"
  }]
  // orderType = [{
  //   "id": "1",
  //   "name": "所有"
  // }, {
  //   "id": "2",
  //   "name": "未付款"
  // }, {
  //   "id": "4",
  //   "name": "已付款"
  // }]
  alert = {
    message: "",
    type: "success"
  }
  tip = false;
  loading = false;
  errorMessage = ""
  currentPage: number = 1;
  totalItems: number = 100; // total numbar of page not items 
  pageSize: number = 10;
  dataSource = [];
  constructor(private router: Router, private daterangepickerOptions: DaterangepickerConfig, private _sanitizer: DomSanitizer, private orderService: orderService) {

    this.daterangepickerOptions.settings = {
      locale: {
        format: 'YYYY-MM-DD',
        applyLabel: "应用",
        cancelLabel: "取消",
        autoUpdateInput: false,
        "daysOfWeek": [
          "日",
          "一",
          "二",
          "三",
          "四",
          "五",
          "六"
        ],
      },
      alwaysShowCalendars: false,
    };
  }
  config = {
    select: false,//是否需要单选框
    headStyle: 'fixed',//是否固定顶部
    serial: true,//是否有序号
    currentPage: this.currentPage,
    pageSize: this.pageSize
  }
  columns = [{
    title: '订单编号',
    dataIndex: 'number',
    width: 150,
  }, {
    title: '订单时间',
    dataIndex: 'created_at',
    width: 150,
    render: (text) => {
      return this.getDom(`
		  			<span>${moment(text).format("YYYY/MM/DD HH:mm:ss")}</span>
	  			`);
    }
  }, {
    title: '购买人',
    dataIndex: 'user',
    width: 150,
    render: (text) => {
      return this.getDom(`
		  			<span>${text.nickname}</span>
	  			`);
    }
  }, {
    title: '订单状态',
    dataIndex: 'status',
    width: 150,
    render: (text) => {
      switch (text) {
        case "ORDER_STATUS_NULL":
          return this.getDom(`
		  			<span>所有</span>
	  			`);
        case "ORDER_STATUS_NOTPAID":
          return this.getDom(`
		  			<span>未付款</span>
	  			`);
        case "ORDER_STATUS_PAYING":
          return this.getDom(`
		  			<span>正在付款</span>
	  			`);
        case "ORDER_STATUS_PAYED":
          return this.getDom(`
		  			<span>已付款</span>
	  			`);
        case "ORDER_STATUS_CLOSED":
          return this.getDom(`
		  			<span>订单取消</span>
	  			`);
        default:
          return this.getDom(`
		  			<span>无效</span>
	  			`);
      }

    }
  }, {
    title: '订单详细信息',
    dataIndex: 'items',
    width: 200,
    render: (text) => {
      let result = "";
      let number = "";
      let currency = "￥"
      text.map((item, i) => {
        if (item.item_type == "ITEM_TYPE_FILTER") {
          item.snapshots.map((items, index) => {
            if (items.key == "number") {
              number = items.value
            }
          })
          result += item.item_name + `<br/>` + number + `<br/>` +
            `
		  			<span style="color:#EC9D1C">${currency + item.real_price}</span>
	  			`
        }

      })
      return this.getDom(`
		  			<span>${result}</span>
	  			`);
    }
  }, {
    title: '操作',
    dataIndex: 'id',
    render: (text) => {
      return this.getDom(`
		  			<a data-event=lookUser style="color: #3D95D1;text-decoration: underline;cursor: pointer;">查看用户</a>
		  			<a data-event=cancelOrder style="color: #E94646;text-decoration: underline;cursor: pointer;">取消订单</a>
	  			`);
    }
  }]
  selectedDate(value: any) {
    this.startTime = moment(value.start._d).format();
    this.endTime = moment(value.end._d).format();
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.status)
  }
  onPageChange(data) {
    if (!data.currentPage) {
      data.currentPage = 1;
    }
    this.config.currentPage = data.currentPage;
    this.currentPage = data.currentPage;
    this.loading = true;
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.status)
  }
  calendarEventsHandler(data) {

  }
  handleDelete(data) {

  }
  handleModify(data) {

  }
  onSelect(data) {

  }
  getDom(str) {
    return this._sanitizer.bypassSecurityTrustHtml(str)
  }
  lookUser(data) {
    // console.log(data)
    localStorage.setItem("userUuid", data.user.id);
    window.open(window.location.href.split('content/')[0] + "content/user");
  }
  cancelOrder(data) {

  }
  changeOrderStatus(data) {
    this.status = data.status
    this.statusValue = data;
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.status)
  }
  changeOrderType(data) {
    this.typeValue = data.name;
  }
  ngOnInit() {
     if(localStorage.getItem("user")&&JSON.parse(localStorage.getItem("user")).uuid){
        
      }else{
      this.router.navigate(['/login']);
    }
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.status)
  }
  serchUserBy() {

  }
  showAlert(message: string, type: string) {
    this.alert = {
      message: message,
      type: type
    }
    this.tip = true;

    let that = this;
    setTimeout(function () {
      that.tip = false;
    }, 2000)
  }
  closeWindow(data) {
    this.window = false;
  }
  export() {
    this.window = true;
  }
  reset() {
    this.currentPage = 1;
    this.startTime = "";
    this.endTime = "";
    this.status = "";
    this.config.currentPage = 1;
    this.daterangepickerOptions.settings = {
      locale: {
        format: 'YYYY-MM-DD',
        applyLabel: "应用",
        cancelLabel: "取消",
        autoUpdateInput: false,
        "daysOfWeek": [
          "日",
          "一",
          "二",
          "三",
          "四",
          "五",
          "六"
        ],
      },
      alwaysShowCalendars: false,
    };
    this.getList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime, this.status)
  }
  getList(offsetHead, offsetTail, startTime, endTime, status) {
    this.loading = true;
    let data = {
      "orderBy": "id",
      "IsDesc": true,
      "offsetHead": offsetHead,
      "offsetTail": offsetTail,
      "startTime": startTime,
      "endTime": endTime,
      "where": {
        "status": status
      }
    }
    this.orderService.getList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        this.loading = false;
        this.dataSource = list.data.list;
        this.totalItems = list.data.count;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });
  }
}



