import { Component } from '@angular/core';
import { Router } from '@angular/router';
@Component({
  selector: 'app-bar',
  templateUrl: './bar.component.html',
  styleUrls: ['./bar.component.less']
})
export class BarComponent {
  //菜单列表
  menus: any = [
    {
      "id": 1, "name": "用户管理", "childrens": [
        { "id": 10, "name": "用户信息", "link": "user" },
        { "id": 11, "name": "黑名单", "link": "black" }
      ]
    }, { "id": 2, "name": "照片管理", "link": "photo" },
    { "id": 3, "name": "订单管理", "link": "order" },
    { "id": 4, "name": "数据统计", "link": "statistical" },
    {
      "id": 5, "name": "运营设置", "childrens": [
        { "id": 50, "name": "滤镜设置", "link": "filter" },
        { "id": 51, "name": "活动页设置", "link": "activity" },
        { "id": 52, "name": "马甲号生成", "link": "majia" }
      ]
    },
    {
      "id": 6, "name": "举报管理", "childrens": [
        { "id": 60, "name": "举报管理", "link": "reportadmin" },
        { "id": 61, "name": "举报类型管理", "link": "reporttype" }
      ]
    }
  ]
  constructor(private router: Router) { }
  child: number;//子链接的编号
  choose;//选择的名字
  chooseChild;//选择的链接
  logoutWindow = false;//登出窗口是否打开
  //初始化
  ngOnInit() {
    this.choose = window.location.href.split('content/')[1];
    this.menus.map((item, i) => {
      if (item.childrens) {
        item.childrens.map((child, index) => {
          if (child.link == this.choose) {
            this.choose = item.name;
            this.chooseChild = child.link;
          }
        })
      }

    })
  }
  //登出确认
  logoutOk() {
    this.logoutWindow = false;
    localStorage.removeItem("user")
    this.router.navigate(['/login']);
  }
  //登出窗口取消
  logoutCancel() {
    this.logoutWindow = false;
  }
  //登出窗口打开
  logout() {
    this.logoutWindow = true;
  }
  //进入链接，有子集的展现子集
  goLink(e, data) {
    e.stopPropagation()
    localStorage.removeItem("userUuid")
    if (data.childrens) {
      this.showChild(data)
    } else if (data.link) {
      this.router.navigate(['../content/' + data.link]);
      this.choose = data.link;
      this.chooseChild = undefined;
      this.hideChild();
    }
  }
  //展现子集
  showChildMenu(e, data) {
    e.stopPropagation()
    if (data.childrens) {
      this.showChild(data)
    }
  }
  //加载完成后监听全局的click
  ngAfterViewInit() {
    document.body.addEventListener("click",
      this.hideChild.bind(this));
  }
  //进入子集的链接
  goChildLink(e, data, child) {
    e.stopPropagation()
    localStorage.removeItem("userUuid")
    this.choose = data.name;
    this.chooseChild = child.link;
    this.router.navigate(['../content/' + child.link]);
    this.hideChild();
  }
  //展现子集
  showChild(data) {
    if (data.childrens) {
      this.child = data.id;
    }
  }
  //隐藏子集
  hideChild() {
    this.child = undefined;
  }
}



