import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { filter } from './../../../config';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
@Injectable()
export class filterService {
    private getListUrl = filter.list;  // URL to web API
    private updateUrl = filter.update;
    private importFilterUrl = filter.importFilterUrl;  // URL to web API
    private importNatureUrl = filter.importNatureUrl;
    private importMoodUrl = filter.importMoodUrl;
    constructor(private http: Http) { }
  public importFilter(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.importFilterUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
     public importNature(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.importNatureUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
     public importMood(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.importMoodUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    getList(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
            headers.append( 'X-Tuso-Authentication-Token',JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.getListUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
 updateEdit(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
            headers.append( 'X-Tuso-Authentication-Token',JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.put(this.updateUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
createEdit(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
            headers.append( 'X-Tuso-Authentication-Token',JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.updateUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    private extractData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }
}


