import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { setCalendar } from './../../../config';
import axios from 'axios';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
@Injectable()

export class filterEditService {
    private uploadFilterCoverURL: string = setCalendar.uploadFilterCoverUrl;
    private uploadMoodCover: string = setCalendar.uploadMoodCover;
    private deleteFilterUrl: string = setCalendar.deleteFilter;
    private deleteMoodUrl: string = setCalendar.deleteMood;
    private uploadFilterZip: string = setCalendar.uploadFilterZip;
    private updateMoodUrl: string = setCalendar.updateMood;
    private updateFilterUrl: string = setCalendar.updateFilter;
    private moodViewUrl: string = setCalendar.moodView;
    private filterViewUrl: string = setCalendar.filterView;
    private natures = setCalendar.naturesUrl;
    constructor(private http: Http) { }

    filterView(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.get(this.filterViewUrl + "/" + data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    deleteNatures(data): Promise<any> {
        return new Promise((resolve, rej) => {
            axios({
                method: 'delete',
                url: this.natures,
                data: data,
                headers: {
                    "Content-Type": "application/json",
                    'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token
                },
            })
                .then((res) => {
                    if (res.status == 200) {
                        resolve(res.data.data)
                    }
                })
        })
    }
    deleteFilters(data): Promise<any> {
        return new Promise((resolve, rej) => {
            axios({
                method: 'delete',
                url: this.deleteFilterUrl,
                data: data,
                headers: {
                    "Content-Type": "application/json",
                    'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token
                },
            })
                .then((res) => {
                    if (res.status == 200) {
                        resolve(res.data.data)
                    }
                })    
        })
    }
    deleteMoods(data): Promise<any> {
        return new Promise((resolve, rej) => {
            axios({
                method: 'delete',
                url: this.deleteMoodUrl,
                data: data,
                headers: {
                    "Content-Type": "application/json",
                    'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token
                },
            })
                .then((res) => {
                    if (res.status == 200) {
                        resolve(res.data.data)
                    }
                })    
        })
    }
    filterCover(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.uploadFilterCoverURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    filterZip(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.uploadFilterZip, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    moodCover(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.uploadMoodCover, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    updateMood(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.put(this.updateMoodUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    updateFilter(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.put(this.updateFilterUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    newMood(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.updateMoodUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    newFilter(data: any): Observable<any> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('X-Tuso-Authentication-Token', JSON.parse(localStorage.getItem("user")).token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.updateFilterUrl, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    // createEdit(data: any): Observable<any> {
    //         let headers = new Headers({ 'Content-Type': 'application/json' });
    //             headers.append( 'X-Tuso-Authentication-Token',JSON.parse(localStorage.getItem("user")).token);
    //         let options = new RequestOptions({ headers: headers });
    //         return this.http.post(this.updateUrl, data, options)
    //             .map(this.extractData)
    //             .catch(this.handleError);
    //     }
    private extractData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }
}


