import { Component, ViewEncapsulation } from '@angular/core';
import { statisticalService } from "./statistical.service"
import { DaterangepickerConfig } from 'ng2-daterangepicker';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser'
import { Router } from '@angular/router';
var moment = require('moment');
@Component({
  selector: 'app-statistical',
  templateUrl: './statistical.component.html',
  styleUrls: ['./statistical.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class StatisticalComponent {
  status = "filter";
  currentPage = 1;
  pageSize = 10;
  totalItems = 0;
  loading = false;
  startTime = moment(new Date(new Date().getFullYear(), 1, 1)).format('YYYY-MM-DD') + 'T00:00:00+08:00';
  endTime = moment(this.getLastDay(new Date().getFullYear(), new Date().getMonth() + 1)).format('YYYY-MM-DD') + 'T23:59:59+08:00';
  // startTime=moment(new Date().setDate(new Date().getDate()-6)).format('YYYY-MM-DD');
  // endTime=moment(new Date()).format('YYYY-MM-DD');
  config = {
    select: false,//是否需要单选框
    headStyle: 'fixed',//是否固定顶部
    serial: true,//是否有序号
    currentPage: this.currentPage,
    pageSize: this.pageSize
  }
  errorMessage = ""
  dataSource;
  tip = false;
  alert = {
    message: "",
    type: "success"
  }
  constructor(private router:Router,private statisticalService: statisticalService, private _sanitizer: DomSanitizer, private daterangepickerOptions: DaterangepickerConfig) {
    this.daterangepickerOptions.settings = {
      locale: {
        format: 'YYYY-MM-DD',
        applyLabel: "应用",
        cancelLabel: "取消",
        autoUpdateInput: false,
        "daysOfWeek": [
          "日",
          "一",
          "二",
          "三",
          "四",
          "五",
          "六"
        ],

      },

      "opens": "left",
      alwaysShowCalendars: false,
    };
  }
  columnsQrc = [{
    title: '日期',
    dataIndex: 'current_date',
    width: 150
  }, {
    title: '新增扫二维码数量',
    dataIndex: 'increase_count',
    width: 150

  }, {
    title: '累计扫二维码数量',
    dataIndex: 'total_count',

  }]
  columnsFilter = [{
    title: '编号',
    dataIndex: '_id',
    width: 150

  }, {
    title: '使用总数',
    dataIndex: 'total_count',
    width: 150

  }, {
    title: '“每日”使用数',
    dataIndex: 'free_count'
  }]
  columnsUser = [{
    title: '日期',
    dataIndex: 'current_date',
    width: 150
  }, {
    title: '新增扫二维码数量',
    dataIndex: 'increase_count',
    width: 150

  }, {
    title: '累计扫二维码数量',
    dataIndex: 'total_count',

  }]
  getDom(str) {
    return this._sanitizer.bypassSecurityTrustHtml(str)
  }
  changeStatus(data) {
    this.status = data;
    if (data == "user") {

    } else if (data == "filter") {
      this.startTime = moment(new Date(new Date().getFullYear(), 1, 1)).format('YYYY-MM-DD') + 'T00:00:00+08:00';
      this.endTime = moment(this.getLastDay(new Date().getFullYear(), new Date().getMonth() + 1)).format('YYYY-MM-DD') + 'T23:59:59+08:00';
      this.getfilterUsedList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime)
    } else {
      this.startTime=moment(new Date().setDate(new Date().getDate()-6)).format('YYYY-MM-DD');
      this.endTime=moment(new Date()).format('YYYY-MM-DD');
      this.getQrcList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime)
    }
  }
  showAlert(message: string, type: string) {
    this.alert = {
      message: message,
      type: type
    }
    this.tip = true;
    let that = this;
    setTimeout(function () {
      that.tip = false;
    }, 2000)
  }
  ngOnInit() {
     if(localStorage.getItem("user")&&JSON.parse(localStorage.getItem("user")).uuid){
      }else{
      this.router.navigate(['/login']);
    }
    // this.getQrcList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize,this.startTime,this.endTime)
    this.getfilterUsedList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime)
  }
  export() {

  }
  onPageChange(data) {
    if (!data.currentPage) {
      data.currentPage = 1;
    }
    this.config.currentPage = data.currentPage;
    this.currentPage = data.currentPage;
    this.getfilterUsedList((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize, this.startTime, this.endTime)
  }
  getLastDay(year, month) {
    var new_year = year;    //取当前的年份  
    var new_month = month++;//取下一个月的第一天，方便计算（最后一天不固定）  
    if (month > 12)            //如果当前大于12月，则年份转到下一年  
    {
      new_month -= 12;        //月份减  
      new_year++;            //年份增  
    }
    var new_date = new Date(new_year, new_month, 1);                //取当年当月中的第一天  
    return (new Date(new_date.getTime() - 1000 * 60 * 60 * 24));//获取当月最后一天日期  
  }
  selectedDate(value: any) {
    this.startTime = moment(value.start._d).format();
    this.endTime = moment(value.end._d).format();
  }
  getfilterUsedList(offsetHead, offsetTail, startTime, endTime) {
    this.loading = true;
    let data = {
      "IsDesc": true,
      "offsetHead": offsetHead,
      "offsetTail": offsetTail,
      "startTime": startTime,
      "endTime": endTime
    }
    this.statisticalService.getfilterUsedList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        // console.log(list)
        this.loading = false;
        this.dataSource = list.data.list;
        //  this.dataSource = list.data;
        this.totalItems = list.data.count;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });
  }
  getQrcList(offsetHead, offsetTail, startTime, endTime) {
    this.loading = true;
    let data = {
      "orderBy": "id",
      "IsDesc": true,
      "offsetHead": offsetHead,
      "offsetTail": offsetTail,
      "startTime": startTime,
      "endTime": endTime
    }
    this.statisticalService.getQrcList(data).subscribe(list => {
      this.loading = false;
      if (list.data) {
        this.loading = false;
        // this.dataSource = list.data.list;
        this.dataSource = list.data;
        this.totalItems = list.data.count;
      }
      if (list.errors) {
        this.showAlert(list.errors[0].messagee, "error")
      }
    },
      error => {
        this.loading = false;
        this.errorMessage = "服务器异常，请联系工程部！";
        this.showAlert(this.errorMessage, "error")
      });
  }

}



