import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './content/login/login.component';
import { IndexComponent } from './content/index/index.component';
import { BlackComponent } from './content/black/black.component';
import { FilterComponent } from './content/filter/filter.component';
import { OrderComponent } from './content/order/order.component';
import { PhotoComponent } from './content/photo/photo.component';
import { MajiaComponent } from './content/majia/majia.component';
import { ActivityComponent } from './content/activity/activity.component';
import { StatisticalComponent } from './content/statistical/statistical.component';
import { ReportAdminComponent } from './content/reportadmin/reportadmin.component';
import { ReportTypeComponent } from './content/reporttype/reporttype.component';


import { UserComponent } from './content/user/user.component';
const routes: Routes = [  
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login',  component: LoginComponent },
  { path: 'content',
        component: IndexComponent,
        children: [
          {
            path: 'user',
            component: UserComponent
          },
          {
            path: 'black',
            component: BlackComponent
          },
          {
            path: 'photo',
            component: PhotoComponent
          },
          {
            path: 'order',
            component: OrderComponent
          },
          {
            path: 'statistical',
            component: StatisticalComponent
          },
          {
            path: 'filter',
            component: FilterComponent
          },
          {
            path: 'activity',
            component: ActivityComponent
          },
          {
            path: 'majia',
            component: MajiaComponent
          },
          {
            path: 'reportadmin',
            component: ReportAdminComponent
          },
          {
            path: 'reporttype',
            component: ReportTypeComponent
          }
        ]
      }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes,{ useHash: true} )],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}


