import { Component,Input,Output,EventEmitter } from '@angular/core';
import _ from "underscore" 

@Component({
  selector: 'app-selectTag',
  templateUrl: './selectTag.component.html',
  styleUrls: ['./selectTag.component.less']
})
export class SelectTagComponent {
@Input() title;
@Input() tags;
@Input() userTag
newTag="";
selectTags=[];
@Output() tagsClo = new EventEmitter<any>();
@Output() pushTag = new EventEmitter<any>();

handleChange(e,data){
  var even = _.find(this.selectTags, function(num){
      return num==data.uuid;
     });
    if(even||even==0){
      this.selectTags=_.without(this.selectTags, data.uuid);
    }else{
      this.selectTags.push(data.uuid)
    }
}
selectTag(data){
  var even = _.find(this.selectTags, function(num){
      return num==data.uuid;
     });
     if(even==0){
       return true;
     }
	 return even;
}
ngOnChanges(){
   let temp=[]
   this.userTag.map((item,i)=>{
      temp.push(item.uuid)
   })
this.selectTags=temp;
}
 ngOnInit(){
   let temp=[]
    this.userTag.map((item,i)=>{
     temp.push(item.uuid)
   })
   this.selectTags=temp;
 }
}






