import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { statistical } from './../../../config';
import axios from 'axios';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
@Injectable()

export class statisticalService {
private qrcListURL: string = statistical.qrcList;
private filterUsedURL=statistical.filterUsed;
    constructor(private http: Http) { }

getQrcList(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.qrcListURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    getfilterUsedList(data: any): Observable<any> {
        let headers = new Headers({ 'X-Tuso-Authentication-Token': JSON.parse(localStorage.getItem("user")).token });
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.filterUsedURL, data, options)
            .map(this.extractData)
            .catch(this.handleError);
    }
    private extractData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }
}


